<template>
    <div class="viewport">
        <div class="row">
            <div class="col-lg-12">
                <blockquote>
                    <p class="text-primary"></p>
                </blockquote>
            </div>
        </div>
        <div class="center-form">
            <div class="row">
                <div class="form-horizontal">
                    <div class="form-group">
                        <div class="col-lg-3 text-right control-label"></div>
                        <div class="col-lg-9"><div class="hr-line-dashed"></div></div>
                    </div>

                    <div class="form-group" :class="{'has-error':new_pwd_err}">
                        <label class="col-lg-3 control-label"></label>
                        <div class="col-lg-6">
                            <input type="text" name="description" v-model="new_pwd" class="form-control">
                        </div>
                        <div class="col-lg-3">
                            <button class="btn btn-primary btn-block" @click.prevent="chgPwd"> </button>
                        </div>
                    </div>

                    <br>
                    <br>
                    <br>

                    <div class="form-group">
                        <div class="col-lg-3 text-right control-label"></div>
                        <div class="col-lg-9"><div class="hr-line-dashed"></div></div>
                    </div>

                    <div class="add-email-m-l">
                        <div class="row">
                            <div class="col-lg-12">
                                <div class="panel panel-default">
                                <div class="panel-heading"></div>
                                    <ul class="list-group">
                                        <template v-for="(item,i) in email_list">
                                            <li class="list-group-item">
                                                <div class="row">
                                                    <div class="col-lg-5 text-ellipsis">{{item.email}}</div>
                                                    <div class="col-lg-5">{{item.remark}}</div>
                                                    <div class="col-lg-2">
                                                        <a href="###" @click.prevent="delEmail(item.email)"><i class="fa fa-minus-circle text-danger"></i></a>
                                                    </div>
                                                </div>
                                            </li>
                                        </template>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        
                        <div class="row">
                            <div class="col-lg-5" :class="{'has-error':new_email_err}">
                                <input type="text" name="description" placeholder="" v-model="new_email" @keydown.enter="addEmail" class="form-control">
                            </div>
                            <div class="col-lg-5">
                                <input type="text" name="description" placeholder="" v-model="remark" @keydown.enter="addEmail" class="form-control">
                            </div>
                            <div class="col-lg-2">
                                <button class="btn btn-primary btn-block" @click.prevent="addEmail"> </button>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-lg-12" :class="{'has-error':new_email_err}">
                                <span class="help-block m-b-none text-warning"></span>
                            </div>
                        </div>
                    </div>
                    



                    

                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import {
        tip,
        ajax
    } from '../tools/tools';

    export default {
        data: function () {
            return {
                new_pwd:'',
                new_pwd_err:false,
                email_list:[],

                new_email:'',
                new_email_err:false,
                remark:''
            }
        },
        methods: {
            chgPwd: function () {
                var self = this;
                var isPass = true;
                if ($.trim(self.new_pwd).length == 0) {
                    self.new_pwd_err = true;
                    isPass = false;
                } else {
                    self.new_pwd_err = false;
                }
                                
                if (isPass) {
                    ajax.changePassword({
                        param: {
                            password: self.new_pwd,
                        },
                        cb:function(data){
                            tip.succ('');
                        },
                        failCb:function(){
                    
                        }
                    });
                } else {
                    tip.err('');
                }
            },
            getEmailList(){
                var self = this;
                ajax.getCustomerDetail({
                    param: {
                        username: self.$root.USER_NAME,
                    },
                    cb:function(data){
                        self.email_list = data.data.email;
                    },
                    failCb:function(){
                
                    }
                })
            },
            addEmail(){
                var self = this;
                var isPass = true;
                if (self.checkEmailText(self.new_email) == false) {
                    self.new_email_err = true;
                    isPass = false;
                } else {
                    self.new_email_err = false;
                }
                                
                if (isPass) {
                    ajax.addEmail({
                        param: {
                            email: self.new_email,
                            remark:self.remark
                        },
                        cb:function(data){
                            tip.succ(''+self.new_email+' ');
                            self.getEmailList();
                            self.new_email = '';
                            self.remark = '';
                        },
                        failCb:function(){
                    
                        }
                    });
                } else {
                    tip.err('');
                }
            },
            delEmail(email){
                var self = this;
                ajax.delEmail({
                    param: {
                        email:email
                    },
                    cb:function(data){
                        tip.succ(''+email+' ');
                        self.getEmailList();
                    },
                    failCb:function(){
                
                    }
                });
            },
            checkEmailText(email_text){
                var re = /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/;
                return re.test(email_text);
            }
        },
        mounted(){
            var self = this;
            self.getEmailList();
        }
    }
</script>
<style scoped>
.add-email-m-l{
    position: relative;
    left: 105px;
    width:625px;
}
</style>



