import '../vendor/cors/jquery-ajax-transport-xdomainrequest'
import toastr from '../vendor/toastr/toastr'

import {cfg} from '../config/config.js'
import {router} from '../config/router.js'

var tip = (function() {
    toastr.options = {
        'closeButton': true,
        'debug': false,
        'newestOnTop': true,
        'progressBar': true,
        'positionClass': 'toast-top-center',
        'preventDuplicates': false,
        'onclick': null,
        'showDuration': '300',
        'hideDuration': '1000',
        'timeOut': '3000',
        'extendedTimeOut': '1000',
        'showEasing': 'swing',
        'hideEasing': 'linear',
        'showMethod': 'fadeIn',
        'hideMethod': 'fadeOut'
    };

    return {
        info:toastr.info,
        succ:toastr.success,
        warn:toastr.warning,
        err:toastr.error
    }
})();

var ajax = (function(){
    function send(url,ajaxName){
        if(!url)tip.err('url');

        return function(arg){
            var fullurl = url;
            if(localStorage.coc_token){
                fullurl = url+'?CO_SSID_OP='+localStorage.coc_token
            }

            var opt = {
                url: fullurl,
                type: 'post',
                dataType: 'json',
                xhrFields: {
                    withCredentials: true
                }
            }

            if(Object.prototype.toString.call(arg.param)=='[object FormData]'){
                opt.data = arg.param;
                opt.cache = false;
                opt.processData = false;
                opt.contentType = false;
            } else {
                opt.data = JSON.stringify(arg.param?arg.param:{});
            }

            $.ajax(opt).done(function(data){
                if(data.errCode==0){
                    arg.cb(data);
                } else if(data.errCode==20001){
                    tip.err('');
                    delete localStorage.coc_token;
                    delete localStorage.coc_username;
                    arg.loginFailCb?arg.loginFailCb():null;
                    
                    var loginObj = {name:'login'};
                    if(router.app.$root.$route.name == 'fbdetailpage'){
                        loginObj.query = {
                            redirect: router.app.$root.$route.fullPath
                        }
                    }

                    router.replace(loginObj);
                } else {
                    tip.err(data.errInfo);
                    arg.failCb?arg.failCb(data):null;
                }
            }).fail(function(){
                tip.err(!ajaxName?url:ajaxName);
                arg.failCb?arg.failCb({errCode:30000}):null;
            });
        };
    }

    return {
        login:send(cfg.baseUrl+'/Api.php/Customer/login',''),
        sendFeedbackEmail:send(cfg.baseUrl+'/Api.php/Customer/sendFeedbackEmail',''),
        getCustomerDetail:send(cfg.baseUrl+'/Api.php/Customer/getCustomerDetail',''),
        changePassword:send(cfg.baseUrl+'/Api.php/Customer/changePassword','-'),
        addEmail:send(cfg.baseUrl+'/Api.php/Customer/addEmail',''),
        delEmail:send(cfg.baseUrl+'/Api.php/Customer/delEmail',' '),
        addFeedback:send(cfg.baseUrl+'/Api.php/Customer/addFeedback',''),
        applyOpenFeedback:send(cfg.baseUrl+'/Api.php/Customer/applyOpenFeedback',''),
        getFeedbackList:send(cfg.baseUrl+'/Api.php/Customer/getFeedbackList',''),
        getFeedbackDetail:send(cfg.baseUrl+'/Api.php/Customer/getFeedbackDetail',''),
        getFeedbackComment:send(cfg.baseUrl+'/Api.php/Customer/getFeedbackComment',''),
        addFeedbackComment:send(cfg.baseUrl+'/Api.php/Customer/addFeedbackComment',''),
        getFeedbackMaterial:send(cfg.baseUrl+'/Api.php/Customer/getFeedbackMaterial',''),
        feedbackAccept:send(cfg.baseUrl+'/Api.php/Customer/feedbackAccept',''),
        feedbackRefuse:send(cfg.baseUrl+'/Api.php/Customer/feedbackRefuse',''),
        getMessageList:send(cfg.baseUrl+'/Api.php/Customer/getMessageList',''),
        getMessageDetail:send(cfg.baseUrl+'/Api.php/Customer/getMessageDetail','')
        
        
        
    };
})();

export {
    tip,ajax
}


