<template>
    <div class="file-input-layout" data-wenk="" data-wenk-pos="bottom">
        <button class="btn btn-primary" @click.prevent="doNothing"><i class="fa fa-upload"></i></button>
        <input type="file" :name="ipname" class="file-input" @change="fileSelected">
        <p class="text-muted val-text">{{valText}}</p>
    </div>
</template>
<script>
export default {
    name:'FileInput',
    data(){
        return {
            fileDataUrl:null,
            reader:new FileReader(),
            valText:''
        }
    },
    props:['value','ipname'],
    methods:{
        fileSelected(event){
            var self = this;
            self.valText = event.target.value;
            
            
            if(event.target.value.length>0){
                var file = event.target.files[0];
                self.fileDataUrl = file;
            } else {
                self.fileDataUrl = null;
            }
            
        },
        doNothing(){
            
        }
    },
    watch:{
        value(o,vo){
            if(o==null){
                $(this.$el).find('input').val('');
                this.fileDataUrl = null;
                this.valText = '';
            }
        },
        fileDataUrl(v,ov){
            var self = this;
            this.$emit('input',self.fileDataUrl);
        }
    },
    mounted(){
        var self = this;
        $(this.$el).find('input').val('');
        this.fileDataUrl = '';
        this.valText = '';
    }
}
</script>
<style scoped>
.file-input-layout{
    position: relative;
    max-width: 245px;
    cursor: pointer;
    display: inline-block;
}

.resize-img{
    cursor: pointer;
    transition: all 0.15s ease-in 0.1s;
}

.file-input-layout>.btn.btn-primary{
    width: 40px;
}

.file-input{
    position: absolute;
    width: 45px;
    height: 34px;
    opacity: 0;
    top: 0px;
    left: 0px;
    /*clip:rect(0px 34px 34px 0px);*/
}
.val-text{
    display: inline-block;
    width: 190px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    vertical-align: middle;
}
</style>


