<template>
    <div class="viewport">
        <div class="row">
            <div class="col-lg-12">
                <blockquote>
                    <p class="text-primary"><a href="###" @click.prevent="$router.push({name:'msgpage'})"></a> &gt; </p>
                </blockquote>
            </div>
        </div>
        <div class="center-form">
            <div class="row">
                <div class="form-horizontal">
                    <div class="row">
                        <div class="col-lg-3"></div>
                        <div class="col-lg-8">
                            <p class="form-control-static">
                                <b>{{$root.SYS_MSG.title}}</b>
                            </p>
                        </div>
                    </div>

                    <div class="form-group">
                        <div class="col-lg-2 text-right control-label"></div>
                        <div class="col-lg-9"><div class="hr-line-dashed"></div></div>
                    </div>


                    <div class="form-group">
                        <label class="col-lg-3 control-label"></label>
                        <div class="col-lg-8">
                            <p class="form-control-static">
                                {{$root.SYS_MSG.type=='1'?'':''}}
                            </p>
                        </div>
                    </div>

                    <div class="form-group">
                        <label class="col-lg-3 control-label"></label>
                        <div class="col-lg-8">
                            <p class="form-control-static">
                                {{$root.SYS_MSG.addtime}}
                            </p>
                        </div>
                    </div>

                    <div class="form-group">
                        <label class="col-lg-3 control-label"></label>
                        <div class="col-lg-8">
                            <p class="form-control-static" v-html="$root.SYS_MSG.content">
                            </p>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>
<script>

import {
        tip,
        ajax
    } from '../tools/tools.js'

export default {
    data(){
        return {
            
        }
    },
    watch:{

    },
    methods:{
        getMsgList(){
            var self = this;
            ajax.getMessageList({
                param:{
                    pn:1,
                    ps:10,
                    status:1
                },
                cb:function(data){
                    self.$root.MSG_LIST = data;
                },
                failCb:function(data){
                    
                }
                
            })
        }
    },
    mounted(){
        var self = this;
        ajax.getMessageDetail({
            param:{
                message_id:self.$root.SYS_MSG.id
            },
            cb(data){
                self.getMsgList();
            },
            failCb:function(data){
                
            }
        })
    }
}
</script>
<style scoped>

</style>



