<template>
    <div class="viewport">
        <div class="row">
            <div class="col-lg-12">
                <blockquote>
                    <p class="text-primary"></p>
                </blockquote>
            </div>
        </div>
        <div class="center-form" v-show="!fbCBInfoPanelShow">
            <div class="row">
                <form class="form-horizontal" id="appform" enctype="multipart/form-data">
                    <input type="hidden" name="type" value="6">

                    <div class="form-group" :class="{'has-error':description_err}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> </label>
                        <div class="col-lg-8">
                            <input type="text" name="description" v-model="description" placeholder="20" class="form-control">
                        </div>
                    </div>

                    <div class="form-title m-t-lg">
                        <div class="form-title-l-txt"></div>
                        <div class="form-title-line"></div>
                        <div class="form-title-r-txt"></div>
                    </div>

                    <div class="form-group">
                        <label class="col-lg-3 control-label"></label>
                        <div class="col-lg-8">
                            <textarea name="" v-model="breif" name="detail[breif]" class="form-control" placeholder="" rows="5"></textarea>
                        </div>
                    </div>

                    <div class="form-group">
                        <label class="col-lg-3 control-label"></label>
                        <div class="col-lg-8">
                            <FileInput ipname="file" v-model="other_file"></FileInput>
                        </div>
                    </div>

                    <div class="form-group">
                        <div class="col-lg-offset-3 col-lg-3 text-left">
                            <button class="btn btn-primary btn-block" @click.prevent="checkAddFeedback"> </button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
        <Modal v-if="confirmFBPopShow" @close="confirmFBPopShow=false">
            <div slot="header">
                <h4 class="modal-title text-center"></h4>
            </div>
            <div slot="body" class="text-center">
                <p>,,</p>
            </div>
            <div slot="footer">
                <button type="button" class="btn btn-primary" @click="confirmAddFB()" :disabled="adding">
                <i class="fa fa-refresh fa-spin fa-fw" v-show="adding"></i>
                 
                </button>
            </div>
        </Modal>

        <FbCBInfoPanel v-show="fbCBInfoPanelShow" :fbNumber="fbNumber" :fbId="fbId" @goOnFb="goOnFb()"></FbCBInfoPanel>

    </div>
</template>
<script>
import FileInput from '../components/FileInput.vue'
import DateTimePicker from '../components/DateTimePicker.vue'
import Modal from '../components/Modal.vue'
import FbCBInfoPanel from '../components/FbCBInfoPanel.vue'

import {
        tip,
        ajax
    } from '../tools/tools.js'

export default {
    components:{FileInput,DateTimePicker,Modal,FbCBInfoPanel},
    data(){
        return {
            fd:null,

            description:'',
            description_err:false,

            breif:'',
            other_file:null,

            confirmFBPopShow:false,
            fbId:'',
            fbNumber:'',
            fbCBInfoPanelShow:false,

            adding:false
        }
    },
    watch:{

    },
    methods:{
        checkAddFeedback(){
            var self = this;

            var isPass = true;

            if ($.trim(self.description).length == 0||$.trim(self.description).length > 20) {
                self.description_err = true;
                isPass = false;
            } else {
                self.description_err = false;
            }
            
            if(isPass){
                self.fbNumber = '';
                self.confirmFBPopShow = true;
            } else {
                tip.err('');
            }            

        },
        confirmAddFB(){
            var self = this;
            self.fd = new FormData(document.getElementById('appform'));
            self.adding = true;
            ajax.addFeedback({
                param: self.fd,
                cb:function(data){
                    self.fbNumber = data.data.number;
                    self.confirmFBPopShow = false;
                    self.fbCBInfoPanelShow = true;
                    self.adding = false;
                },
                failCb:function(data){
                    self.adding = false;
                }
            });

            // self.fbNumber = '1';
            // self.confirmFBPopShow = false;
            // self.fbCBInfoPanelShow = true;
        },
        goOnFb(){
            var self = this;
            self.description = '';
            self.description_err = false;
            self.breif = '';
            self.other_file = null;

            self.fbNumber = '';
            self.fbCBInfoPanelShow = false;
        }
    }
}
</script>
<style scoped>

</style>



