<template>
    <div class="viewport">
        <div class="row">
            <div class="col-lg-12">
                <blockquote>
                    <p class="text-primary"><a href="###" @click.prevent="$router.push({name:'requirementpage'})"></a> &gt; </p>
                </blockquote>
            </div>
        </div>
        <div class="center-form" v-show="!fbCBInfoPanelShow">
            <div class="row">
                <form class="form-horizontal" id="appform" enctype="multipart/form-data">
                    <input type="hidden" name="type" value="1">
                    <div class="form-group" :class="{'has-error':appDescriptionErr}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> </label>

                        <div class="col-lg-8">
                            <input type="text" name="description" v-model="appDescription" placeholder="20" class="form-control">
                        </div>
                    </div>
                    <div class="form-title m-t-lg">
                        <div class="form-title-l-txt"></div>
                        <div class="form-title-line"></div>
                        <div class="form-title-r-txt"></div>
                    </div>
                   
                    <div class="form-group" :class="{'has-error':appFileErr}">
                        <label class="col-lg-3 control-label"></label>

                        <div class="col-lg-8">
                            <FileInput ipname="app_file" v-model="appFile"></FileInput>
                        </div>
                    </div>
                    <div class="form-group" :class="{'has-error':appProgramErr}">
                        <label class="col-lg-3 control-label"></label>

                        <div class="col-lg-8">
                            <input type="text" name="detail[app_program]" placeholder="MD5 / " v-model="appProgram" class="form-control">
                            <span class="help-block m-b-none"><span class="text-warning">*</span> </span>
                        </div>
                    </div>
                    <div class="form-title m-t-lg">
                        <div class="form-title-l-txt"></div>
                        <div class="form-title-line"></div>
                        <div class="form-title-r-txt"></div>
                    </div>
                    <div class="form-group">
                        <label class="col-sm-3 control-label"></label>

                        <div class="col-sm-8">
                            <label class="radio-inline">
                                <input type="radio" value="1" name="detail[misjudgment_type]" v-model="misjudgmentType"></label>
                            <label class="radio-inline">
                                <input type="radio" value="2" name="detail[misjudgment_type]" v-model="misjudgmentType"></label>
                        </div>
                    </div>
                    <div class="form-group" :class="{'has-error':misjudgmentResultErr}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> </label>

                        <div class="col-lg-8">
                            <input type="text" name="detail[misjudgment_result]" placeholder=" / " v-model="misjudgmentResult" class="form-control">
                        </div>
                    </div>
                    <div class="form-group" :class="{'has-error':misjudgmentDatetimeErr}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> </label>

                        <div class="col-lg-8">
                            <DateTimePicker v-model="misjudgmentDatetime" :end-date="int_time" dtpname="detail[misjudgment_datetime]"></DateTimePicker>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="col-lg-3 control-label"></label>

                        <div class="col-lg-8">
                            <textarea name="" v-model="brief" name="detail[brief]" class="form-control" placeholder="" rows="5"></textarea>
                        </div>
                    </div>
                    <div class="form-group">
                        <div class="col-lg-offset-3 col-lg-3 text-left">
                            <button class="btn btn-primary btn-block" @click.prevent="checkAddFeedback"> </button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
        <Modal v-if="confirmFBPopShow" @close="confirmFBPopShow=false">
            <div slot="header">
                <h4 class="modal-title text-center"></h4>
            </div>
            <div slot="body" class="text-center">
                <p>,,</p>
            </div>
            <div slot="footer">
                <button type="button" class="btn btn-primary" @click="confirmAddFB()" :disabled="adding">
                <i class="fa fa-refresh fa-spin fa-fw" v-show="adding"></i>
                 
                </button>
            </div>
        </Modal>

        <FbCBInfoPanel v-show="fbCBInfoPanelShow" :fbNumber="fbNumber" :fbId="fbId" @goOnFb="goOnFb()"></FbCBInfoPanel>

    </div>
</template>
<script>
import FileInput from '../components/FileInput.vue'
import DateTimePicker from '../components/DateTimePicker.vue'
import Modal from '../components/Modal.vue'
import FbCBInfoPanel from '../components/FbCBInfoPanel.vue'

import {
        tip,
        ajax
    } from '../tools/tools.js'

export default {
    components:{FileInput,DateTimePicker,Modal,FbCBInfoPanel},
    data(){
        return {
            fd:null,
            appDescription:'',
            appDescriptionErr:false,
            appFile:null,
            appFileErr:false,
            appProgram:'',
            appProgramErr:false,
            misjudgmentType:'',
            misjudgmentResult:'',
            misjudgmentResultErr:false,
            int_time:(new Date().getFullYear())+'-'+(new Date().getMonth()+1)+'-'+(new Date().getDate()),
            misjudgmentDatetime:'',
            misjudgmentDatetimeErr:false,
            brief:'',

            confirmFBPopShow:false,
            fbId:'',
            fbNumber:'',
            fbCBInfoPanelShow:false,

            adding:false
        }
    },
    watch:{

    },
    methods:{
        checkAddFeedback(){
            var self = this;

            var isPass = true;
            if ($.trim(self.appDescription).length == 0||$.trim(self.appDescription).length > 20) {
                self.appDescriptionErr = true;
                isPass = false;
            } else {
                self.appDescriptionErr = false;
            }
            if (!self.appFile&&$.trim(self.appProgram).length == 0) {
                self.appFileErr = true;
                self.appProgramErr = true;
                isPass = false;
            } else {
                self.appFileErr = false;
                self.appProgramErr = false;
            }
            if ($.trim(self.misjudgmentResult).length == 0) {
                self.misjudgmentResultErr = true;
                isPass = false;
            } else {
                self.misjudgmentResultErr = false;
            }
            if ($.trim(self.misjudgmentDatetime).length == 0) {
                self.misjudgmentDatetimeErr = true;
                isPass = false;
            } else {
                self.misjudgmentDatetimeErr = false;
            }

            
            if(isPass){
                self.fbNumber = '';
                self.confirmFBPopShow = true;

            } else {
                tip.err('');
            }

            

        },
        confirmAddFB(){
            var self = this;
            self.fd = new FormData(document.getElementById('appform'));
            self.adding = true;
            ajax.addFeedback({
                param: self.fd,
                cb:function(data){
                    self.fbNumber = data.data.number;
                    self.confirmFBPopShow = false;
                    self.fbCBInfoPanelShow = true;
                    self.adding = false;
                },
                failCb:function(data){
                    self.adding = false;
                }
            });

            // self.fbNumber = '1';
            // self.confirmFBPopShow = false;
            // self.fbCBInfoPanelShow = true;
        },
        goOnFb(){
            var self = this;
            self.appDescription = ''; 
            self.appDescriptionErr = false;
            self.appFile = null;
            self.appFileErr = false;
            self.appProgram = '';
            self.appProgramErr = false;
            self.misjudgmentType = '';
            self.misjudgmentResult = '';
            self.misjudgmentResultErr = false;
            self.misjudgmentDatetime = '';
            self.misjudgmentDatetimeErr = false;
            self.brief = '';

            self.fbNumber = '';
            self.fbCBInfoPanelShow = false;
        }
    }
}
</script>
<style scoped>

</style>



