<template>
    <div class="viewport">
        <div class="row">
            <div class="col-lg-12">
                <blockquote>
                    <div>
                        <p class="text-primary inline-block-p">
                            
                        </p>
                        <div class="blockquote-form text-right small-text">
                            <span></span>
                        </div>
                        <div class="blockquote-form">
                            <div class="form-group">
                                <select class="form-control bot-border" v-model="msg_list_type">
                                    <option value="2"></option>
                                    <option value="1"></option>
                                </select>
                            </div>
                        </div>
                    </div>

                </blockquote>
            </div>
        </div>
        <div class="center-table">
            <div class="row">
                <div class="col-lg-12">
                    <div class="recode-list-layout">
                        <div class="row m-b-lg">
                            <div class="col-lg-3"></div>
                            <div class="col-lg-4"></div>
                            <div class="col-lg-4"></div>
                            <div class="col-lg-1"></div>
                        </div>
                        <template v-for="(msg,i) in msg_list">
                            <div class="recode-row status-1" @click="goToMsgDetail(msg)">
                                <div class="row">
                                    <div class="col-lg-3 text-ellipsis">{{msg.title}}</div>
                                    <div class="col-lg-4 text-ellipsis" v-html="msg.content"></div>
                                    <div class="col-lg-4">{{msg.addtime}}</div>
                                    <div class="col-lg-1 animate-grid text-right">
                                        <i class="fa fa-circle text-success" v-if="msg.status=='0'"></i>
                                        <i class="fa fa-chevron-right" aria-hidden="true"></i>
                                    </div>
                                </div>
                            </div>
                     
                        </template>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
    import Modal from '../components/Modal.vue'
    import {
        tip,
        ajax
    } from '../tools/tools.js'

    export default {
        components: {
            Modal
        },
        data() {
            return {
                msg_list_type:'2',
                msg_list:[]
            }
        },
        watch:{
            msg_list_type(v,ov){
                var self = this;
                self.getMsgList({type:v});
            }
        },
        filters:{
            typeStr(v){
                return v=='1'?'':''
            },
            addtimeStr(v){
                return v.substring(5,v.length-3);
            }
        },
        methods:{
            getMsgList(par,listcb){
                var self = this;
                ajax.getMessageList({
                    param:par,
                    cb:function(data){
                        self.msg_list = data.data.data;
                        if(listcb){
                            listcb(data);
                        }
                    },
                    failCb:function(data){
                        
                    }
                    
                })
            },
            goToMsgDetail(msg){
                var self = this;
                if(msg.type=='2'){
                    var number = msg.number;
                    ajax.getMessageDetail({
                        param:{
                            message_id:msg.id
                        },
                        cb(data){
                            self.getMsgList({pn:1,ps:10,status:1},function(listdata){
                                self.$root.MSG_LIST = listdata;
                            });
                            self.$root.$router.push({
                                name:'fbdetailpage',
                                params:{
                                    number,
                                    routename:'s'
                                }
                            });
                        },
                        failCb:function(data){
                            
                        }
                    })
                    
                } else {
                    self.$root.SYS_MSG = msg;
                    self.$root.$router.push({
                        name:'msgdetailpage'
                    });
                }

                
                // self.msg_box_shown = false;
            },
            getDate(dateStr){
                var dayStr = dateStr.substring(0,10);
                var YYYY = parseInt(dayStr.split('-')[0]);
                var MM = parseInt(dayStr.split('-')[1])-1;
                var DD = parseInt(dayStr.split('-')[2]);

                var timeStr = dateStr.substring(11,dateStr.length);
                var hh = parseInt(timeStr.split(':')[0]);
                var mm = parseInt(timeStr.split(':')[1]);
                var ss = parseInt(timeStr.split(':')[2]);

                return new Date(YYYY,MM,DD,hh,mm,ss);
            }
        },
        mounted(){
            var self = this;
            self.getMsgList({type:self.msg_list_type});
        }
    }
</script>
<style scoped>
    .blockquote-form .form-group {
        margin-bottom: 0px;
    }

    .form-control.bot-border {
        border: none;
        border-bottom: dotted 1px #e5e6e7;
    }

    .inline-block-p {
        display: inline-block;
        margin-bottom: 0;
        margin-right: 30px;
    }

    .blockquote-form {
        display: inline-block;
        margin-left: 10px;
        vertical-align: middle;
    }

    .small-text span {
        font-size: 70%;
    }
    
    .recode-row{
        background-color: #efefef;
        padding: 8px 15px;
        margin-bottom: 10px;
        border-radius: 4px;
        transition: box-shadow 0.25s ease-in-out;
        border-width: 1px;
        border-style: solid;
        cursor: pointer;
    }
    .recode-row:hover{
        box-shadow: 0 1px 3px 0 rgba(0,0,0,.2), 0 1px 1px 0 rgba(0,0,0,.14), 0 2px 1px -1px rgba(0,0,0,.12);
    }
    .recode-row.status-1{
        border-color: #607d8b;
    }

    .animate-grid .fa-chevron-right{
        position: relative;
        left:0px;
        
    }

    .recode-row:hover .animate-grid .fa-chevron-right{
        animation-name:move;
        animation-duration:1.5s;
        animation-timing-function:ease-out;
        animation-iteration-count:infinite;
    }

    @keyframes move{
        0% {left:0px;}
        100% {left:12px;}
    }
</style>


