<template>
    <div class="viewport">
        <div class="row">
            <div class="col-lg-12">
                <blockquote>
                    <p class="text-primary"><a href="###" @click.prevent="$router.push({name:'requirementpage'})"></a> &gt; WIFI</p>
                </blockquote>
            </div>
        </div>
        <div class="center-form" v-show="!fbCBInfoPanelShow">
            <div class="row">
                <form class="form-horizontal" id="appform" enctype="multipart/form-data">
                    <input type="hidden" name="type" value="3">
                    <div class="form-group" :class="{'has-error':descriptionErr}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> </label>

                        <div class="col-lg-8">
                            <input type="text" name="description" v-model="description" placeholder="20" class="form-control">
                        </div>
                    </div>
                    <div class="form-title m-t-lg">
                        <div class="form-title-l-txt"></div>
                        <div class="form-title-line"></div>
                        <div class="form-title-r-txt"></div>
                    </div>
                    <div class="form-group" :class="{'has-error':sdkVersionErr}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> SDK</label>

                        <div class="col-lg-8">
                            <input type="text" name="detail[sdk_version]" placeholder="SDK" v-model="sdkVersion" class="form-control">
                        </div>
                    </div>
                    <div class="form-group" :class="{'has-error':equipmentModelErr}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> </label>

                        <div class="col-lg-8">
                            <input type="text" name="detail[equipment_model]" placeholder="" v-model="equipmentModel" class="form-control">
                        </div>
                    </div>

                    <div class="form-title m-t-lg">
                        <div class="form-title-l-txt"></div>
                        <div class="form-title-line"></div>
                        <div class="form-title-r-txt"></div>
                    </div>

                    <div class="form-group" :class="{'has-error':misjudgmentResultErr}">
                        <label class="col-sm-3 control-label"><span class="text-warning">*</span> </label>

                        <div class="col-sm-8">
                            <label class="radio-inline">
                                <input type="radio" value="1" name="detail[misjudgment_result]" v-model="misjudgmentResult"></label>
                            <label class="radio-inline">
                                <input type="radio" value="2" name="detail[misjudgment_result]" v-model="misjudgmentResult"></label>
                        </div>
                    </div>
                    <div class="form-group" :class="{'has-error':misjudgmentTypeErr}">
                        <label class="col-sm-3 control-label"><span class="text-warning">*</span> </label>

                        <div class="col-sm-8">
                            <label class="radio-inline">
                                <input type="radio" value="1" name="detail[misjudgment_type]" v-model="misjudgmentType">ARP</label>
                            <label class="radio-inline">
                                <input type="radio" value="2" name="detail[misjudgment_type]" v-model="misjudgmentType"></label>
                                <label class="radio-inline">
                                <input type="radio" value="3" name="detail[misjudgment_type]" v-model="misjudgmentType"></label>
                        </div>
                    </div>
                    <div class="form-group" :class="{'has-error':recurrenceProbabilityErr}">
                        <label class="col-sm-3 control-label"><span class="text-warning">*</span> </label>

                        <div class="col-sm-8">
                            <label class="radio-inline">
                                <input type="radio" value="1" name="detail[recurrence_probability]" v-model="recurrenceProbability"></label>
                            <label class="radio-inline">
                                <input type="radio" value="2" name="detail[recurrence_probability]" v-model="recurrenceProbability"></label>
                        </div>
                    </div>

                    <div class="form-group" :class="{'has-error':logFileErr}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> </label>

                        <div class="col-lg-8">
                            <FileInput ipname="log_file" v-model="logFile"></FileInput>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="col-lg-3 control-label"></label>

                        <div class="col-lg-8">
                            <textarea name="" v-model="brief" name="detail[brief]" class="form-control" placeholder="" rows="5"></textarea>
                        </div>
                    </div>
                    <div class="form-group">
                        <div class="col-lg-offset-3 col-lg-3 text-left">
                            <button class="btn btn-primary btn-block" @click.prevent="checkAddFeedback"> </button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
        <Modal v-if="confirmFBPopShow" @close="confirmFBPopShow=false">
            <div slot="header">
                <h4 class="modal-title text-center"></h4>
            </div>
            <div slot="body" class="text-center">
                <p>,,</p>
            </div>
            <div slot="footer">
                <button type="button" class="btn btn-primary" @click="confirmAddFB()" :disabled="adding">
                <i class="fa fa-refresh fa-spin fa-fw" v-show="adding"></i>
                 
                </button>
            </div>
        </Modal>

        <FbCBInfoPanel v-show="fbCBInfoPanelShow" :fbNumber="fbNumber" :fbId="fbId" @goOnFb="goOnFb()"></FbCBInfoPanel>

    </div>
</template>
<script>
import FileInput from '../components/FileInput.vue'
import DateTimePicker from '../components/DateTimePicker.vue'
import Modal from '../components/Modal.vue'
import FbCBInfoPanel from '../components/FbCBInfoPanel.vue'

import {
        tip,
        ajax
    } from '../tools/tools.js'

export default {
    components:{FileInput,DateTimePicker,Modal,FbCBInfoPanel},
    data(){
        return {
            fd:null,
            description:'',
            descriptionErr:false,
            sdkVersion:'',
            sdkVersionErr:false,
            equipmentModel:'',
            equipmentModelErr:false,
            misjudgmentResult:'',
            misjudgmentResultErr:false,
            misjudgmentType:'',
            misjudgmentTypeErr:false,
            recurrenceProbability:'',
            recurrenceProbabilityErr:false,
            logFile:null,
            logFileErr:false,
            brief:'',

            confirmFBPopShow:false,
            fbId:'',
            fbNumber:'',
            fbCBInfoPanelShow:false,

            adding:false
        }
    },
    watch:{

    },
    methods:{
        checkAddFeedback(){
            var self = this;

            var isPass = true;
            if ($.trim(self.description).length == 0||$.trim(self.description).length > 20) {
                self.descriptionErr = true;
                isPass = false;
            } else {
                self.descriptionErr = false;
            }
            if ($.trim(self.sdkVersion).length == 0) {
                self.sdkVersionErr = true;
                isPass = false;
            } else {
                self.sdkVersionErr = false;
            }
            if ($.trim(self.equipmentModel).length == 0) {
                self.equipmentModelErr = true;
                isPass = false;
            } else {
                self.equipmentModelErr = false;
            }
            if ($.trim(self.misjudgmentResult).length == 0) {
                self.misjudgmentResultErr = true;
                isPass = false;
            } else {
                self.misjudgmentResultErr = false;
            }
            if ($.trim(self.misjudgmentType).length == 0) {
                self.misjudgmentTypeErr = true;
                isPass = false;
            } else {
                self.misjudgmentTypeErr = false;
            }
            if ($.trim(self.recurrenceProbability).length == 0) {
                self.recurrenceProbabilityErr = true;
                isPass = false;
            } else {
                self.recurrenceProbabilityErr = false;
            }
            if (!self.logFile) {
                self.logFileErr = true;
                isPass = false;
            } else {
                self.logFileErr = false;
            }
            

            
            if(isPass){
                self.fbNumber = '';
                self.confirmFBPopShow = true;

            } else {
                tip.err('');
            }

            

        },
        confirmAddFB(){
            var self = this;
            self.fd = new FormData(document.getElementById('appform'));
            self.adding = true;
            ajax.addFeedback({
                param: self.fd,
                cb:function(data){
                    self.fbNumber = data.data.number;
                    self.confirmFBPopShow = false;
                    self.fbCBInfoPanelShow = true;
                    self.adding = false;
                },
                failCb:function(data){
                    self.adding = false;
                }
            });

            // self.fbNumber = '1';
            // self.confirmFBPopShow = false;
            // self.fbCBInfoPanelShow = true;
        },
        goOnFb(){
            var self = this;
            self.description = ''; 
            self.descriptionErr = false;
            self.sdkVersion = '';
            self.sdkVersionErr = false;
            self.equipmentModelErr = '';
            self.sdkVersionErr = false;
            self.misjudgmentResult = '';
            self.misjudgmentResultErr = false;
            self.misjudgmentType = '';
            self.misjudgmentTypeErr = false;
            self.recurrenceProbability = '';
            self.recurrenceProbabilityErr = false;
            self.logFile = null;
            self.logFileErr = false;
            self.brief = '';

            self.fbNumber = '';
            self.fbCBInfoPanelShow = false;
        }
    }
}
</script>
<style scoped>

</style>



