<template>
    <div class="middle-box text-center loginscreen animated fadeInDown">
        <div class="load-ele login-box">
            <div>
                <h1 class="logo-name">AVL</h1>
            </div>
            <h3 class="m-b-lg">CO</h3>
            <h6></h6>
            <form class="m-t" @submit.prevent="signup()">
                <div class="row">
                    <div class="col-lg-7">
                        <div class="form-group" :class="{'has-error':company_name_err}">
                            <input type="text" class="form-control" placeholder="" v-model="company_name">
                        </div>
                    </div>
                    <div class="col-lg-5">
                        <div class="form-group" :class="{'has-error':part_name_err}">
                            <input type="text" class="form-control" placeholder="" v-model="part_name">
                        </div>
                    </div>
                </div>
                <div class="form-group" :class="{'has-error':email_err}">
                    <input type="text" class="form-control" placeholder="" v-model="email">
                </div>
                <div class="form-group" :class="{'has-error':phone_err}">
                    <input type="text" class="form-control" placeholder="" v-model="phone">
                </div>
                <button class="btn btn-primary block full-width" type="submit"> </button>
                <div class="form-group">
                    <button class="btn btn-link pull-right" @click.prevent="gotoLogin()"> <i class="fa fa-long-arrow-right"></i></button>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
    import {
        tip,
        ajax
    } from '../tools/tools';

    export default {
        data: function () {
            return {
                company_name: '',
                company_name_err: false,
                part_name: '',
                part_name_err: false,
                email: '',
                email_err: false,
                phone: '',
                phone_err: false
            }
        },
        methods: {
            signup: function () {
                var self = this;
                var isPass = true;
                if ($.trim(self.company_name).length == 0) {
                    self.company_name_err = true;
                    isPass = false;
                } else {
                    self.company_name_err = false;
                }
                if ($.trim(self.part_name).length == 0) {
                    self.part_name_err = true;
                    isPass = false;
                } else {
                    self.part_name_err = false;
                }
                if ($.trim(self.email).length == 0) {
                    self.email_err = true;
                    isPass = false;
                } else {
                    self.email_err = false;
                }
                if ($.trim(self.phone).length == 0) {
                    self.phone_err = true;
                    isPass = false;
                } else {
                    self.phone_err = false;
                }
                
                if (isPass) {
                    // ajax.login({
                    //     param: {
                    //         username: self.username,
                    //         password: self.password,
                    //     },
                    //     cb:function(data){
                    
                    //         self.$root.$router.replace({
                    //             name:'index'
                    //         });
                    //     },
                    //     failCb:function(){
                    
                    //     }
                    // });
                } else {
                    tip.err(',');
                }
            },
            gotoLogin(){
                this.$router.push({
                    name:'login'
                })
            }
        }
    }
</script>


