import Vue from 'vue'
import VueRouter from 'vue-router'

import login from '../pages/login.vue'
import signup from '../pages/signup.vue'
import chgpwd from '../pages/chgpwd.vue'
import notfound from '../pages/notfound.vue'
import index from '../pages/index.vue'
import requirementpage from '../pages/requirementpage.vue'
import bugpage from '../pages/bugpage.vue'
import otherreqpage from '../pages/otherreqpage.vue'
import workingpage from '../pages/workingpage.vue'
import recodepage from '../pages/recodepage.vue'
import apppage from '../pages/apppage.vue'
import urlpage from '../pages/urlpage.vue'
import wifipage from '../pages/wifipage.vue'
import bankpage from '../pages/bankpage.vue'
import msgpage from '../pages/msgpage.vue'
import msgdetailpage from '../pages/msgdetailpage.vue'
import fbdetailpage from '../pages/fbdetailpage.vue'

Vue.use(VueRouter);

const router = new VueRouter({
  mode: 'history',
  routes:[{
            path: '/',
            redirect: {
                name: 'login'
            }
        },
        {
            path: '/login',
            name: 'login',
            component: login,
            beforeEnter(to, from, next) {
                if (localStorage.coc_token) {
                    router.replace({
                      name: 'index'
                    });
                } else {
                    next();
                }
            }
        },
        {
            path: '/signup',
            name: 'signup',
            component: signup
        },
        {
            path: '/logout',
            name: 'logout',
            beforeEnter(to, from) {
                delete localStorage.coc_token;
                delete localStorage.coc_username;
                router.push({
                    name: 'login'
                });
            }
        },
        {
            path: '/index',
            redirect: {
                name: 'requirementpage'
            },
            name: 'index',
            component: index,
            beforeEnter(to, from, next) {
                if (!localStorage.coc_token) {
                    var loginObj = {name:'login'};
                    if(to.name == 'fbdetailpage'){
                        loginObj.query = {
                            redirect: to.fullPath
                        }
                    }
                    router.replace(loginObj);
                } else {
                    next();
                }
                next();
            },
            children: [
                {path:'requirementpage', name:'requirementpage', component: requirementpage,
                    children:[
                        {path:'apppage',name:'apppage',component:apppage,
                            beforeEnter(to, from, next) {
                                next();
                            }
                        },
                        {path:'urlpage',name:'urlpage',component:urlpage,
                            beforeEnter(to, from, next) {
                                next();
                            }
                        },
                        {path:'wifipage',name:'wifipage',component:wifipage,
                            beforeEnter(to, from, next) {
                                next();
                            }
                        },
                        {path:'bankpage',name:'bankpage',component:bankpage,
                            beforeEnter(to, from, next) {
                                next();
                            }
                        }
                    ]
                },
                {path:'bugpage', name:'bugpage', component: bugpage},
                {path:'otherreqpage', name:'otherreqpage', component: otherreqpage},
                {path:'workingpage', name:'workingpage', component: workingpage},
                {path:'recodepage', name:'recodepage', component: recodepage},
                {path:'msgpage', name:'msgpage', component: msgpage},
                {path:'msgdetailpage', name:'msgdetailpage', component: msgdetailpage},
                {path:'fbdetailpage/:routename/:number', name:'fbdetailpage', component: fbdetailpage},
                {path:'chgpwd', name:'chgpwd',component:chgpwd}
            ]
        },
        {
            path: '*',
            component: notfound
        }
    ]
})

export {router}


