<template>
    <div class="row">
        <br><br><br><br>
        <div class="col-lg-12 text-center">
            <div class="step-icon-box donestep">
                <i class="fa fa-check fa-3x"></i>
                <div></div>
            </div>
            <div class="loading-img">
                <span></span>
                <span></span>
                <span></span>
            </div>
            <div class="step-icon-box willstep">
                <i class="fa fa-comments fa-3x"></i>
                <div></div>
            </div>
            <br><br><br><br>
            <h3>,</h3>
            <br><br>
            <p>, <strong class="fb-number-strong">{{fbNumber}}</strong> , <a href="###" @click.prevent="goToWorkingPage()"></a> </p>
            <br><br>
            <div class="text-center">
                <button class="btn btn-primary" @click="goToIndex()"></button>&nbsp;&nbsp;&nbsp;<button class="btn btn-link" @click="goOnFb()"></button>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:'FbCBInfoPanel',
    data(){
        return {
            
        }
    },
    props:{
        fbNumber:{
            type:String,
            default:''
        },
        fbId:{
            type:String,
            default:''
        }
    },
    methods:{
        goToIndex(){
            var self = this;
            self.$root.$router.push({
                name:'requirementpage'
            });
        },
        goOnFb(){
            this.$emit('goOnFb');
        },
        goToWorkingPage(){
            var self = this;
            self.$root.$router.push({
                name:'workingpage'
            });
        }
    },
    watch:{
        
    },
    mounted(){
        var self = this;
    }
}
</script>
<style scoped>
.fb-number-strong{
    font-size: 140%;
}

.donestep{
    background: #ddd;
}

.willstep{
    background: #f5f5f5;
}

.step-icon-box{
    display: inline-block;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    
    box-sizing: border-box;
    padding: 10px;
}

.loading-img{
    display: inline-block;
    margin: 0 10px;
    height: 80px;
    box-sizing: border-box;
    vertical-align:top;
    padding-top: 25px;
}

.loading-img>span{
    vertical-align: middle;
    display: inline-block;
    background-color: #828282;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin: 5px;
    opacity: 0.2;
    animation-name:p1;
    animation-duration:1s;
    animation-timing-function:ease-in-out;
    animation-iteration-count:infinite;
    animation-direction:alternate;
}

.loading-img>span:nth-child(1){
    animation-delay:0s;
}
.loading-img>span:nth-child(2){
    animation-delay:0.5s;
}
.loading-img>span:nth-child(3){
    animation-delay:1s;
}

@keyframes p1{
  0% {opacity:0.2;}
  100% {opacity:1;}
}
</style>


