<template>
    <div class="viewport">
        <div class="row">
            <div class="col-lg-12">
                <blockquote>
                    <div>
                        <p class="text-primary inline-block-p">
                            {{title}}
                        </p>
                    </div>

                </blockquote>
            </div>
        </div>
        <div class="recode-panel">
            <div class="list-page-prev list-page" v-if="pn>1" @click.prevent="goToPage(-1)">
                <i class="fa fa-arrow-circle-up fa-fw" aria-hidden="true"></i>
                10
            </div>
            <div class="list-page-next list-page" v-if="pn<total_pn" @click.prevent="goToPage(1)">
                <i class="fa fa-arrow-circle-down fa-fw" aria-hidden="true"></i>
                10
            </div>
            <div class="recode-list-layout">
                
                <template v-for="(arrv,arri) in data_list">
                    <div :key="arrv.key" class="recode-row" @click="showDetail(arrv,true)" 
                    :class="{'status-1':arrv.status=='1','status-2':arrv.status=='2','status-3':arrv.status=='3','curr-detail':arrv.number==number}">
                        <div class="recode-grid grid-1">{{arrv.number}}</div>
                        <div class="recode-grid grid-2">{{arrv.description}}</div>
                        <div class="recode-grid grid-3">{{arrv.status |statusStr}}</div>
                        
                    </div>
                </template>
                <div class="fb-layout">
                    <div class="fb-title">
                        <div class="row">
                            <div class="col-lg-3">
                                <span class="text-ellipsis fb-title-text-4">
                                <i class="fa fa-chevron-left fa-fw"  @click="backToList"></i>
                                </span>
                                <span class="text-ellipsis fb-title-text-8">{{detail.number}}</span>
                                <span class="text-ellipsis fb-title-text-12">{{detail.description}}</span>
                            </div>
                            <div class="col-lg-5">
                                <span class="small text-muted">{{detail.type | typeStr}}</span>
                            </div>
                            <div class="col-lg-4 text-right">
                                <span class="small text-muted">{{detail.updatetime!=null?detail.updatetime:detail.addtime}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="fb-contorl-bar">
                        <div class="row">
                            <div class="col-lg-4">
                                <div class="btn-group">
                                    <button class="btn" type="button" :class="{'btn-success':show_panel==0,'btn-white':show_panel==1}" @click.prevent="show_panel=0"></button>
                                    <button class="btn" type="button" :class="{'btn-success':show_panel==1,'btn-white':show_panel==0}" @click.prevent="show_panel=1"></button>
                                </div>
                            </div>
                            <div class="col-lg-4 text-center">
                                <div class="btn-group">
                                    <div class="approve-bar" v-show="detail.status=='3'&&detail.accept_fail!='1'">
                                        <span v-if="detail.refuse=='0'"></span>
                                        <span v-if="detail.refuse=='1'" class="text-warning"></span>
                                        <span class="v-line"></span>
                                        <span @click.prevent="approveFb">
                                            <i class="fa fa-check fa-fw text-success"></i>
                                        </span>
                                        <span @click.prevent="rejectFb">
                                            <i class="fa fa-times fa-fw"></i>
                                        </span>
                                    </div>
                                    <div class="approve-bar" v-show="detail.status=='2'">
                                        <span>{{detail.predict_delivery_time}}</span>
                                    </div>
                                    <div class="approve-bar" v-show="detail.accept_fail=='1'">
                                        <span class="text-warning"></span>
                                    </div>
                                    
                                </div>
                            </div>
                            <div class="col-lg-4"></div>
                        </div>
                    </div>
                    <div class="fb-scroll-layout" v-show="show_panel==0">
                        <div class="fb-timeline-bar">
                            <FbTimeline :feedback-status="detail.feedback_status"></FbTimeline>
                        </div>
                        <div class="fb-comment-bar">
                            <div class="row">
                                <div class="form-horizontal no-mb col-lg-12">
                                    <div class="form-title" :class="{'text-muted':detail.username!=$root.USER_NAME,'text-warning':detail.username==$root.USER_NAME}">
                                        <div class="form-title-l-txt" :class="{'text-muted':detail.username!=$root.USER_NAME,'text-warning':detail.username==$root.USER_NAME}">{{detail.username}}</div>
                                        <div class="form-title-line"></div>
                                        <div class="form-title-r-txt text-muted">{{detail.addtime}}</div>
                                    </div>

                                    <template v-for="(v,k,i) in fbdetail">
                                        <div class="form-group" v-if="v!=null">
                                            <label class="col-lg-2 control-label">{{k | attrKeyStr(detail.type)}}</label>

                                            <div class="col-lg-8 m-b-lg">
                                                <p class="form-control-static" v-if="k!='file'&&k!='log_file'&&k!='app_file'">
                                                    <template v-if="k=='misjudgment_object'&&detail.type=='2'">
                                                        <div v-for="urlitem in v">
                                                            {{urlitem}}
                                                        </div>
                                                    </template>
                                                    <template v-else>
                                                        {{v | attrValue(k,detail.type)}}
                                                    </template>
                                                </p>
                                                <p class="form-control-static" v-else-if="v==''">
                                                    {{v | attrValue(k,detail.type)}}
                                                </p>
                                                <p class="form-control-static" v-else>
                                                    <a :href="getFileUrl(v)" target="_blank" :download="getFileName(v)">{{v | attrValue(k,detail.type)}}</a>
                                                </p>
                                            </div>
                                        </div>
                                        
                                    </template>
                                    
                                    <template v-for="(item,key) in comment_list">
                                        <div class="form-title">
                                            <div class="form-title-l-txt" :class="{'text-muted':item.username!=$root.USER_NAME,'text-warning':item.username==$root.USER_NAME}">{{item.username}}</div>
                                            <div class="form-title-line"></div>
                                            <div class="form-title-r-txt text-muted">{{item.addtime}}</div>
                                        </div>
                                        <div class="form-group" v-if="item.detail.conclusion">
                                            <label class="col-lg-2 control-label"></label>
                                            <div class="col-lg-8 m-b-lg">
                                                <p class="form-control-static">
                                                    {{item.detail.conclusion}}
                                                </p>
                                            </div>
                                        </div>
                                        <div class="form-group" v-if="item.detail.reason">
                                            <label class="col-lg-2 control-label"></label>
                                            <div class="col-lg-8 m-b-lg">
                                                <p class="form-control-static">
                                                    {{item.detail.reason}}
                                                </p>
                                            </div>
                                        </div>
                                        <div class="form-group" v-if="item.detail.delivery_time">
                                            <label class="col-lg-2 control-label"></label>
                                            <div class="col-lg-8 m-b-lg">
                                                <p class="form-control-static">
                                                    {{item.detail.delivery_time}}
                                                </p>
                                            </div>
                                        </div>
                                        <div class="form-group" v-if="item.detail.description">
                                            <label class="col-lg-2 control-label"></label>
                                            <div class="col-lg-8 m-b-lg">
                                                <p class="form-control-static">
                                                    {{item.detail.description}}
                                                </p>
                                            </div>
                                        </div>
                                        <div class="form-group" v-if="item.detail.file&&item.detail.file.length>0">
                                            <label class="col-lg-2 control-label"></label>
                                            <div class="col-lg-8 m-b-lg">
                                                <p class="form-control-static" v-for="fileitem in item.detail.file">
                                                    <a :href="getCommentFileUrl(fileitem)" :download="fileitem.name" target="_blank">{{fileitem.name}}</a>
                                                </p>
                                            </div>
                                        </div>
                                    </template>
                                </div>
                            </div>
                        </div>
                        <div class="fb-addcomment-bar col-lg-12" v-if="detail.close=='0'">
                            <div class="row">
                                <form class="form-horizontal" id="appform" enctype="multipart/form-data">
                                    <input type="hidden" name="feedback_id" :value="detail.id">

                                    <div class="form-title">
                                        <div class="form-title-l-txt text-muted"></div>
                                        <div class="form-title-line"></div>
                                        <div class="form-title-r-txt text-muted"></div>
                                    </div>

                                    <div class="form-group">
                                        <label class="col-lg-2 control-label"></label>
                                        <div class="col-lg-6">
                                            <textarea name="" v-model="description" name="description" class="form-control" placeholder="" rows="5"></textarea>
                                        </div>
                                    </div>

                                    <div class="form-group">
                                        <label class="col-lg-2 control-label"></label>
                                        <div class="col-lg-6">
                                            
                                            <template v-for="(fileitem,i) in comment_upload_list">
                                                <p :key="fileitem.id">
                                                    <FileInput ipname="file[]"></FileInput>
                                                    <i v-if="i!=0||comment_upload_list.length>1" class="fa fa-minus-circle fa-2x del-file-btn text-danger" @click.prevent="delUpload(fileitem)"></i>
                                                </p>
                                            </template>
                                            <p class="m-t">
                                                <a href="###" @click.prevent="addUpload"></a>
                                            </p>
                                        </div>
                                    </div>
                                    
                                    <div class="form-group">
                                        <div class="col-lg-offset-2 col-lg-3 text-left">
                                            <button class="btn btn-primary btn-block" @click.prevent="addComment"> </button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>

                    <div class="fb-scroll-layout" v-show="show_panel==1">
                        <div class="fb-comment-bar">
                            <div class="row">
                                <div class="form-horizontal">
                                    <div class="form-title">
                                        <div class="form-title-l-txt"></div>
                                        <div class="form-title-line"></div>
                                        <div class="form-title-r-txt">{{detail.addtime}}</div>
                                    </div>

                                    <template v-for="(v,k,i) in fbdetail">
                                        <div class="form-group" v-if="v!=null">
                                            <label class="col-lg-2 control-label">{{k | attrKeyStr(detail.type)}}</label>

                                            <div class="col-lg-8">
                                                <p class="form-control-static" v-if="k!='file'&&k!='log_file'&&k!='app_file'">
                                                    <template v-if="k=='misjudgment_object'&&detail.type=='2'">
                                                        <div v-for="urlitem in v">
                                                            {{urlitem}}
                                                        </div>
                                                    </template>
                                                    <template v-else>
                                                        {{v | attrValue(k,detail.type)}}
                                                    </template>
                                                </p>
                                                <p class="form-control-static" v-else-if="v==''">
                                                    {{v | attrValue(k,detail.type)}}
                                                </p>
                                                <p class="form-control-static" v-else>
                                                    <a :href="getFileUrl(v)" target="_blank" :download="getFileName(v)">{{v | attrValue(k,detail.type)}}</a>
                                                </p>
                                            </div>
                                        </div>
                                    </template>
                                    
                                    <div class="form-title">
                                        <div class="form-title-l-txt"></div>
                                        <div class="form-title-line"></div>
                                        <div class="form-title-r-txt"></div>
                                    </div>
                                    <template v-for="(item,key) in material_list">
                                        
                                        <div class="form-group">
                                            <label class="col-lg-3 control-label"></label>
                                            <div class="col-lg-4">
                                                <p class="form-control-static">
                                                    <a :href="getCommentFileUrl(item)" target="_blank"  :download="item.name">{{item.name}}</a>
                                                </p>
                                            </div>
                                            <div class="col-lg-2">
                                                <p class="form-control-static text-navy">
                                                    {{item.username}}
                                                </p>
                                            </div>
                                            <div class="col-lg-3">
                                                <p class="form-control-static text-navy">
                                                    {{item.datetime}}
                                                </p>
                                            </div>
                                        </div>
                                    </template>

                                    <div class="form-title">
                                        <div class="form-title-l-txt"></div>
                                        <div class="form-title-line"></div>
                                        <div class="form-title-r-txt"></div>
                                    </div>
                                    <template v-for="(item,key) in result_list">
                                        
                                        <div class="form-group">
                                            <label class="col-lg-3 control-label"></label>
                                            <div class="col-lg-4">
                                                <p class="form-control-static">
                                                    <a :href="getCommentFileUrl(item)" target="_blank" :download="item.name">{{item.name}}</a>
                                                </p>
                                            </div>
                                            <div class="col-lg-2">
                                                <p class="form-control-static text-navy">
                                                    {{item.username}}
                                                </p>
                                            </div>
                                            <div class="col-lg-3">
                                                <p class="form-control-static text-navy">
                                                    {{item.datetime}}
                                                </p>
                                            </div>
                                        </div>
                                    </template>
                                </div>
                            </div>
                        </div>
                    </div>
                    
                </div>
            </div>
                
        </div>

    </div>
</template>
<script>
    import Modal from '../components/Modal.vue'
    import FbTimeline from '../components/FbTimeline.vue'
    import FileInput from '../components/FileInput.vue'
    import {cfg} from '../config/config.js'
    import {
        tip,
        ajax
    } from '../tools/tools.js'

    export default {
        components: {
            Modal,FbTimeline,FileInput
        },
        data() {
            return {
                title:'',
                show_panel:0,

                total: 0,
                pn:1,
                ps:10,
                total_pn:1,
                data_list:[],

                close_route_name:'',
                number:'',
                detail:{},
                fbdetail:{},

                comment_list:[],

                description:'',
                comment_upload_list:[{id:1}],

                material_list:[],
                result_list:[]

                
            }
        },
        watch: {
            number(v,ov){
                
            },
            $route(v,ov){
                var self = this;
                this.init();
            },
            detail:{
                deep: true,
                handler: function (val, oval) {
                    var v = val.detail;
                    var obj = {};
                    obj.bug_description = v.bug_description!=undefined?v.bug_description:null;

                    obj.app_program = v.app_program!=undefined?v.app_program:null;
                    obj.sdk_version = v.sdk_version!=undefined?v.sdk_version:null;
                    obj.equipment_model = v.equipment_model!=undefined?v.equipment_model:null;
                    obj.android_version = v.android_version!=undefined?v.android_version:null;
                    obj.architecture = v.architecture!=undefined?v.architecture:null;


                    obj.misjudgment_type = v.misjudgment_type!=undefined?v.misjudgment_type:null;
                    obj.misjudgment_object = v.misjudgment_object!=undefined?v.misjudgment_object:null;
                    obj.recurrence_probability = v.recurrence_probability!=undefined?v.recurrence_probability:null;
                    obj.misjudgment_result = v.misjudgment_result!=undefined?v.misjudgment_result:null;
                    obj.misjudgment_datetime = v.misjudgment_datetime!=undefined?v.misjudgment_datetime:null;

                    // obj.app_file = v.app_file!=undefined?(v.app_file==''?[]:v.app_file):[{path:null,name:null}];
                    // obj.log_file = v.log_file!=undefined?(v.log_file==''?[]:v.log_file):[{path:null,name:null}];
                    // obj.file = v.file!=undefined?(v.file==''?[]:v.file):[{path:null,name:null}];

                    obj.app_file = v.app_file!=undefined?v.app_file:null;
                    obj.log_file = v.log_file!=undefined?v.log_file:null;
                    obj.file = v.file!=undefined?v.file:null;
                    
                    obj.breif = v.breif!=undefined?v.breif:null;

                    this.fbdetail = obj;
                }
            }
        },
        filters:{
            statusStr(v){
                var sObj = {
                    '1':'-',
                    '2':'-',
                    '3':'-',
                    '4':'-'
                }

                return sObj[v];
            },
            typeStr(v){
                var tObj = {
                    '1':'',
                    '2':'URL',
                    '3':'WiFi',
                    '4':'',
                    '5':'bug',
                    '6':'',
                }
                return tObj[v];
            },
            attrKeyStr(v,type){
                var obj = {};
                obj.bug_description = 'bug';

                obj.app_program = '';
                obj.sdk_version = 'SDK';
                obj.equipment_model = '';
                obj.android_version = '';
                obj.architecture = '';


                obj.misjudgment_type = '';
                obj.misjudgment_object = '';
                obj.recurrence_probability = '';
                obj.misjudgment_result = '';
                obj.misjudgment_datetime = '';

                obj.app_file = '';
                obj.log_file = '';
                obj.file = '';
                if(type=='2'){
                    obj.file = 'url';
                }
                
                obj.breif = '';
                return obj[v];
            },
            attrValue(v,k,t){
                var var_str = v;
                var mtObj = {
                    '1':{
                        '1':'','2':''
                    },
                    '2':{
                        '1':'App','2':'','3':''
                    }
                }
      
                var mrOjb = {
                    '1':'','2':''
                }
                var rpObj = {
                    '1':'','2':''
                }


                if(k=='app_file'||k=='log_file'||k=='file'){
                    var_str = v==''?'-':v[0].name;
                }
                if(k=='misjudgment_type'){
                    var dtype = t == '3'?'2':'1';
                    var_str = mtObj[dtype][v];
                }
                if(k=='misjudgment_result'){
                    if(t=='1'){
                        var_str = v;
                    }else {
                        var_str = mrOjb[v];
                    }
                }
                if(k=='recurrence_probability'){
                    var_str = rpObj[v];
                }
                                
                return var_str;
            }
        },
        methods: {
            init(){
                var self = this;
                self.number = self.$root.$route.params.number;
                
                var titleObj = {
                    'w':'',
                    'r':'',
                    's':''
                }
                self.title = titleObj[self.$root.$route.params.routename];

                self.getDetail({
                    ps:self.ps,
                    number:self.number
                },function(data){
                    var close_route_name = data.data.list_type=='underway'?'workingpage':'recodepage';
                    self.close_route_name = close_route_name;
                    self.pn = parseInt(data.data.page);

                    var par = {};
                    par.history = data.data.list_type=='underway'?'0':'1';
                    par.ps = self.ps;
                    par.pn = self.pn;                    

                    self.getFbList(par);
                    self.getFbComment(data.data.data.id);
                    self.getFbMaterial(data.data.data.id);
                });                
            },
            showDetail(item,resetShowPanel){
                var self = this;
                self.number = item.number;

                if(resetShowPanel){
                    self.show_panel = 0;
                }

                self.getDetail({
                    ps:self.ps,
                    number:self.number
                },function(data){
                    self.getFbComment(data.data.data.id);
                    self.getFbMaterial(data.data.data.id);
                });
            },
            getDetail(par,cb){
                var self = this;
                ajax.getFeedbackDetail({
                    param:par,
                    cb:function(data){
                        self.detail = data.data.data;
                        if(cb){
                            cb(data);
                        }
                    },
                    failCb:function(data){
                        
                    }
                })
            },
            getFbComment(id,cb){
                var self = this;
                ajax.getFeedbackComment({
                    param:{feedback_id:id},
                    cb:function(data){
                        self.comment_list = data.data;
                        if(cb){
                            cb(data);
                        }
                    },
                    failCb:function(data){
                        
                    }
                })
            },
            getFbMaterial(id,cb){
                var self = this;
                ajax.getFeedbackMaterial({
                    param:{feedback_id:id},
                    cb:function(data){
                        self.material_list = data.data.material;
                        self.result_list = data.data.result
                        if(cb){
                            cb(data);
                        }
                    },
                    failCb:function(data){
                        
                    }
                })
            },
            getFbList(par,cb) {
                var self = this;
                
                ajax.getFeedbackList({
                    param:par,
                    cb:function(data){
                        //{"data":{"total":"6","wait_for_accept":1,"unread_update":0,"data":[{"id":"29","number":"OM 7","description":"1","status":"1","type":"3","close":"0","refuse":"0","addtime":"2017-06-21 21:46:48","updatetime":null,"donetime":null,"predict_delivery_time":null,"actual_delivery_time":null},{"id":"28","number":"OM 6","description":"1","status":"1","type":"1","close":"0","refuse":"0","addtime":"2017-06-21 21:04:54","updatetime":null,"donetime":null,"predict_delivery_time":null,"actual_delivery_time":null},{"id":"27","number":"OM 5","description":"aaa","status":"1","type":"1","close":"0","refuse":"0","addtime":"2017-06-21 21:03:16","updatetime":null,"donetime":null,"predict_delivery_time":null,"actual_delivery_time":null},{"id":"26","number":"OM 4","description":"1","status":"1","type":"2","close":"0","refuse":"0","addtime":"2017-06-21 20:28:45","updatetime":null,"donetime":null,"predict_delivery_time":null,"actual_delivery_time":null},{"id":"5","number":"OM 2","description":"","status":"3","type":"1","close":"0","refuse":"0","addtime":"2017-06-09 16:51:12","updatetime":null,"donetime":null,"predict_delivery_time":null,"actual_delivery_time":null},{"id":"4","number":"OM 1","description":"","status":"1","type":"1","close":"0","refuse":"0","addtime":"2017-06-09 10:58:07","updatetime":null,"donetime":null,"predict_delivery_time":null,"actual_delivery_time":null}]},"errCode":0,"errInfo":""}
                        self.total = parseInt(data.data.total);
                        self.total_pn = Math.ceil(self.total/10);
                        self.data_list = data.data.data;
                        if(cb){
                            cb(data);
                        }
                    },
                    failCb:function(data){
                        
                    }
                })
            },
            goToPage(num){
                var self = this;
                var par = {};
                    par.history = self.close_route_name=='workingpage'?'0':'1';
                    par.ps = self.ps;

                    self.pn = self.pn + num;
                    par.pn = self.pn;
                self.getFbList(par,function(data){
                    var first_fb = data.data.data[0];
                    self.showDetail(first_fb);
                });
                

            },
            addComment(){
                var self = this;
                var fd = new FormData(document.getElementById('appform'));
                ajax.addFeedbackComment({
                    param: fd,
                    cb:function(data){
                        self.getFbComment(self.detail.id);
                        self.getFbMaterial(self.detail.id);
                        self.description = '';
                        self.comment_upload_list = [];
                        setTimeout(function(){
                            self.comment_upload_list.push({id:1});
                        },300);


                    },
                    failCb:function(data){
                        
                    }
                })
            },
            addUpload(){
                var self = this;
                var i = self.comment_upload_list.length;
                self.comment_upload_list.push({id:i+1});
            },
            delUpload(o){
                var self = this;
                self.comment_upload_list.splice(this.comment_upload_list.indexOf(o), 1);
            },
            approveFb(){
                var self = this;
                ajax.feedbackAccept({
                    param:{feedback_id:self.detail.id},
                    cb:function(data){
                        self.showDetail({number:self.detail.number});
                    }
                });
            },
            rejectFb(){
                var self = this;
                ajax.feedbackRefuse({
                    param:{feedback_id:self.detail.id},
                    cb:function(data){
                        self.showDetail({number:self.detail.number});
                    }
                });
            },
            backToList(){
                var self = this;
                // if(self.$root.$route.params.routename=='s'){
                //     self.$root.$router.go(-1);
                // } else {
                    self.$root.$router.push({
                        name:self.close_route_name
                    });
                // }
                
            },
            getFileUrl(v){
                return cfg.downloadUrl+v[0].path;
            },
            getFileName(v){
                return v[0].name;
            },
            getCommentFileUrl(v){
                return cfg.downloadUrl+v.path;
            }
            
        },
        mounted(){
            var self = this;
            self.init();
        }
    }
</script>
