        padding: 0 5px;
    }
    .recode-grid.grid-3{
        width: 85px;
        border-right:solid 3px;
        
    }
    .recode-row.status-1 .recode-grid.grid-3{color:#77a5b0;border-right-color:#77a5b0;}
    .recode-row.status-2 .recode-grid.grid-3{color:#03a9f4;border-right-color:#03a9f4;}
    .recode-row.status-3 .recode-grid.grid-3{color:#00bcd4;border-right-color:#00bcd4;}
    
    .list-page{
        position: absolute;
        left: 30px;
        z-index: 3;
        width: 340px;
