<template>
    <div>
        <div class="viewport" v-show="showMainViewport">
            <div class="row m-b-lg m-l m-r">
                <div class="col-lg-8">
                    <blockquote>
                        <p></p>
                    </blockquote>
                </div>
                <div class="col-lg-4">
                    <div class="form-inline  pull-right">
                        <div class="form-group">
                            <label class="control-label"></label>
                            <SwitchBtn v-model="otherServiceShow"></SwitchBtn>
                        </div>
                    </div>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-3" v-if="appOpened">
                    <router-link :to="{name:'apppage'}" tag="div" active-class="active" class="widget lavender-bg text-center router-widget">
                        <i class="fa fa-briefcase fa-4x"></i>
                        <br>
                        <br>
                        <h3><span></span></h3>
                        <br>
                        <br>
                        <br>
                        <div class="m-r-lg m-l-lg text-muted small line-h-lg"></div>
                    </router-link>
                </div>
                <transition name="slide-fade" v-if="!appOpened">
                    <div class="col-lg-3" v-show="!otherServiceShow">
                        <div>
                            <transition name="hor-flip">
                            <div class="widget gray-bg text-center router-widget flip-widget" @click.prevent="showApply('appApplyShow')"  v-show="!appApplyShow">
                                <i class="fa fa-briefcase fa-4x"></i>
                                <br><br>
                                <h3><span></span></h3>
                                <br><br><br>
                                <div class="m-r-lg m-l-lg text-muted small line-h-lg"></div>
                            </div>
                            </transition>
                            <transition name="hor-flip">
                            <div class="widget gray-bg text-center router-widget flip-widget" @click.prevent="showApply('appApplyShow')"  v-show="appApplyShow">
                                
                                <h4><span></span></h4>
                                <br>
                                <p class="m-r-lg m-l-lg text-muted small line-h-lg">:</p>
                                <br><br><br>
                                <p>
                                    <button class="btn btn-link" @click.prevent.stop="downloadDes()"></button> | 
                                    <button class="btn btn-link" @click.prevent.stop="showApplyServicePop(0)"></button>
                                </p>
                                
                            </div>
                            </transition>
                        </div>
                    </div>
                </transition>


                <div class="col-lg-3" v-if="urlOpened">
                    <router-link :to="{name:'urlpage'}" tag="div" active-class="active" class="widget lavender-bg text-center router-widget">  
                        <i class="fa fa-link fa-4x"></i>
                        <br><br>
                        <h3><span>url</span></h3>
                        <br><br><br>
                        <div class="m-r-lg m-l-lg text-muted small line-h-lg">urlURL</div>
                    </router-link>
                </div>
                <transition name="slide-fade" v-if="!urlOpened">
                    <div class="col-lg-3" v-show="!otherServiceShow">
                        <div>
                            <transition name="hor-flip">
                            <div class="widget gray-bg text-center router-widget flip-widget" @click.prevent="showApply('urlApplyShow')"  v-show="!urlApplyShow">
                                <i class="fa fa-link fa-4x"></i>
                                <br><br>
                                <h3><span>url</span></h3>
                                <br><br><br>
                                <div class="m-r-lg m-l-lg text-muted small line-h-lg">urlURL</div>
                            </div>
                            </transition>
                            <transition name="hor-flip">
                            <div class="widget gray-bg text-center router-widget flip-widget" @click.prevent="showApply('urlApplyShow')"  v-show="urlApplyShow">
                                <h4><span></span></h4>
                                <br>
                                <p class="m-r-lg m-l-lg text-muted small line-h-lg">urlURL</p>
                                <br><br><br>
                                <p>
                                    <button class="btn btn-link" @click.prevent.stop="downloadDes()"></button> | 
                                    <button class="btn btn-link" @click.prevent.stop="showApplyServicePop(1)"></button>
                                </p>
                            </div>
                            </transition>
                        </div>
                    </div>
                </transition>


                <div class="col-lg-3" v-if="wifiOpened">
                    <router-link :to="{name:'wifipage'}" tag="div" active-class="active" class="widget lavender-bg text-center router-widget">
                        <i class="fa fa-wifi fa-4x"></i>
                        <br><br>
                        <h3><span>wifi</span></h3>
                        <br><br><br>
                        <div class="m-r m-l text-muted small line-h-lg">wifiARP</div>
                    </router-link>
                </div>
                <transition name="slide-fade" v-if="!wifiOpened">
                    <div class="col-lg-3" v-show="!otherServiceShow">
                        <div>
                            <transition name="hor-flip">
                            <div class="widget gray-bg text-center router-widget flip-widget" @click.prevent="showApply('wifiApplyShow')"  v-show="!wifiApplyShow">
                                <i class="fa fa-wifi fa-4x"></i>
                                <br><br>
                                <h3><span>wifi</span></h3>
                                <br><br><br>
                                <p class="m-r m-l text-muted small line-h-lg">wifiARP</p>
                            </div>
                            </transition>
                            <transition name="hor-flip">
                            <div class="widget gray-bg text-center router-widget flip-widget" @click.prevent="showApply('wifiApplyShow')"  v-show="wifiApplyShow">
                                <h4><span></span></h4>
                                <br>
                                <p class="m-r m-l text-muted small line-h-lg">wifiARP</p>
                                <br><br><br>
                                <p>
                                    <button class="btn btn-link" @click.prevent.stop="downloadDes()"></button> | 
                                    <button class="btn btn-link" @click.prevent.stop="showApplyServicePop(2)"></button>
                                </p>
                            </div>
                            </transition>
                        </div>
                    </div>
                </transition>

                <div class="col-lg-3" v-if="bankOpened">
                    <router-link :to="{name:'bankpage'}" tag="div" active-class="active" class="widget lavender-bg text-center router-widget">
                        <i class="fa fa-archive fa-4x"></i>
                        <br><br>
                        <h3><span></span></h3>
                        <br><br><br>
                        <div class="m-r-lg m-l-lg text-muted small line-h-lg"></div>
                    </router-link>
                </div>
                <transition name="slide-fade" v-if="!bankOpened">
                    <div class="col-lg-3" v-show="!otherServiceShow">
                        <div>
                            <transition name="hor-flip">
                            <div class="widget gray-bg text-center router-widget flip-widget" @click.prevent="showApply('bankApplyShow')"  v-show="!bankApplyShow">
                                <i class="fa fa-archive fa-4x"></i>
                                <br><br>
                                <h3><span></span></h3>
                                <br><br><br>
                                <div class="m-r-lg m-l-lg text-muted small line-h-lg"></div>
                            </div>
                            </transition>
                            <transition name="hor-flip">
                            <div class="widget gray-bg text-center router-widget flip-widget" @click.prevent="showApply('bankApplyShow')"  v-show="bankApplyShow">
                                <h4><span></span></h4>
                                <br>
                                <p class="m-r-lg m-l-lg text-muted small line-h-lg"></p>
                                <br><br><br>
                                <p>
                                    <button class="btn btn-link" @click.prevent.stop="downloadDes()"></button> | 
                                    <button class="btn btn-link" @click.prevent.stop="showApplyServicePop(3)"></button>
                                </p>
                            </div>
                            </transition>
                        </div>
                    </div>
                </transition>
                
            </div>
        </div>
        <Modal v-if="applyServicePopShow" @close="applyServicePopShow=false">
            <div slot="header">
                <h4 class="modal-title text-center">"{{applyServiceStr}}"</h4>
            </div>
            <div slot="body" class="text-center">
                <p>2</p>
            </div>
            <div slot="footer">
                <button type="button" class="btn btn-primary" @click="applyPopOk()"></button>
            </div>
        </Modal>
        <router-view></router-view>
    </div>

</template>
<script>
    import SwitchBtn from '../components/SwitchBtn.vue'
    import Modal from '../components/Modal.vue'
    import {
        tip,
        ajax
    } from '../tools/tools'

    export default {
        components: {
            SwitchBtn,Modal
        },
        data() {
            return {
                appOpened:false,
                urlOpened:false,
                wifiOpened:false,
                bankOpened:false,

                showMainViewport:true,
                otherServiceShow: false,
                appApplyShow:false,
                urlApplyShow:false,
                wifiApplyShow:false,
                bankApplyShow:false,

                applyServicePopShow:false,
                applyServiceNameArr:['','URL','WIFI',''],
                applyServiceStr:''
            }
        },
        watch:{
            $route(v,ov){
                if(v.name=='apppage'||v.name=='urlpage'||v.name=='wifipage'||v.name=='bankpage'){
                    this.showMainViewport = false;
                } else {
                    this.showMainViewport = true;
                }
            },
            otherServiceShow(v,ov){
                localStorage.otherServiceShow = v;
                if(v==false){
                    this.appApplyShow = false;
                    this.urlApplyShow = false;
                    this.wifiApplyShow = false;
                    this.bankApplyShow = false;
                }
            }
        },
        props: {
            
        },
        methods: {
            showApply(key){
                this[key] = !this[key];
            },
            showApplyServicePop(key){
                this.applyServiceStr = this.applyServiceNameArr[key];
                this.applyServicePopShow = true;
            },
            downloadDes(){
                
            },
            applyPopOk(){
                var self = this;
                self.applyServicePopShow = false;
                var k = self.applyServiceNameArr.indexOf(self.applyServiceStr)+1;
                ajax.applyOpenFeedback({
                    param: {
                        type:k
                    },
                    cb:function(data){
                        tip.succ('');
                    },
                    failCb:function(data){
                        
                    }
                })
            }
        },
        created(){
            if(localStorage.otherServiceShow){
                this.otherServiceShow = localStorage.otherServiceShow=='true'?true:false;
            }
        },
        mounted(){
            var self = this;
            let name = this.$route.name;
            if(name=='apppage'||name=='urlpage'||name=='wifipage'){
                this.showMainViewport = false;
            } else {
                this.showMainViewport = true;
            }

            

            ajax.getCustomerDetail({
                param:{
                    username:localStorage.coc_username
                },
                cb:function(data){
                    
                    self.$root.ACCESS = data.data.access;
                    self.USE_SCENE = data.data.use_scene;
                    self.$root.AFTER_SALES = data.data.after_sales;
                    self.$root.CUSTOMER_NAME = data.data.customer_name;
                    self.$root.EMAIL = data.data.email;
                    self.$root.ID = data.data.id
                    self.$root.NUMBER = data.data.number;
                    if(self.$root.ACCESS.indexOf('1')!=-1){
                        self.appOpened = true;
                    }
                    if(self.$root.ACCESS.indexOf('2')!=-1){
                        self.urlOpened = true;
                    }
                    if(self.$root.ACCESS.indexOf('3')!=-1){
                        self.wifiOpened = true;
                    }
                    if(self.$root.ACCESS.indexOf('4')!=-1){
                        self.bankOpened = true;
                    }

                },
                failCb:function(data){
                    
                }
            })

                    
            // self.appOpened = true;
            // self.urlOpened = true;
            // self.wifiOpened = true;
            // self.bankOpened = true;
                    
        }
    }
</script>
<style scoped>
.router-widget{
    padding: 60px 0px;
    margin: 0px auto;
    max-width: 260px;
    -webkit-transition: box-shadow 0.4s;
    -moz-transition: box-shadow 0.4s;
    transition: box-shadow 0.4s;
    cursor: pointer;
    min-height: 300px;
}

.line-h-lg{
    line-height: 170%;
}

.router-widget:hover{
    box-shadow: 0 2px 4px -1px rgba(0,0,0,.2), 0 4px 5px 0 rgba(0,0,0,.14), 0 1px 10px 0 rgba(0,0,0,.12); 
}

.slide-fade-enter-active {
  transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-leave-active {
  transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-active {
  transform: translateX(10px);
  opacity: 0;
}


.flip-widget{
    position: absolute;
    width: 100%;
    left: 0px;
    right: 0px;
}

.hor-flip-enter-active,.hor-flip-leave-active{
    transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}

.hor-flip-enter, .hor-flip-leave-active {
    -moz-transform:scaleX(-1);
    -webkit-transform:scaleX(-1);
    -o-transform:scaleX(-1);
    transform:scaleX(-1);
    opacity: 0;
}
</style>



