<template>
    <div class="navbar-side">
        <div class="sidebar-collapse">
            <ul class="nav in">
                <li :class="{'active':showLink=='req'}">
                    <a href="###" @click.prevent="switchLink('req')">
                        <i class="fa fa-commenting"></i>
                        <span class="nav-label"></span><span class="fa arrow"></span>
                    </a>
                    <ul class="nav nav-second-level collapse" :class="{'in':showLink=='req'}">
                        <router-link :to="{name:'requirementpage'}" tag="li" active-class="active">
                            <a href="javascript:">
                                <span class="nav-label"></span>
                            </a>
                        </router-link>
                        <router-link :to="{name:'bugpage'}" tag="li" active-class="active">
                            <a href="javascript:">
                                <span class="nav-label">bug</span>
                            </a>
                        </router-link>
                        <router-link :to="{name:'otherreqpage'}" tag="li" active-class="active">
                            <a href="javascript:">
                                <span class="nav-label"></span>
                            </a>
                        </router-link>
                    </ul>
                </li>
                <li :class="{'active':showLink=='rec'}">
                    <a href="###" @click.prevent="switchLink('rec')">
                        <i class="fa fa-tasks"></i>
                        <span class="nav-label"></span><span class="fa arrow"></span>
                    </a>
                    <ul class="nav nav-second-level collapse"  :class="{'in':showLink=='rec'}">
                        <li :class="{'active':active_fb_list=='w'}"><a href="###" @click.prevent="$root.$router.push({name:'workingpage'});"><span class="nav-label"></span></a></li>
                        <li :class="{'active':active_fb_list=='r'}"><a href="###" @click.prevent="$root.$router.push({name:'recodepage'});"><span class="nav-label"></span></a></li>
                        <!--<router-link :to="{name:'workingpage'}" tag="li" active-class="active">
                            <a href="javascript:">
                                <span class="nav-label"></span>
                            </a>
                        </router-link>
                        <router-link :to="{name:'recodepage'}" tag="li" active-class="active">
                            <a href="javascript:">
                                <span class="nav-label"></span>
                            </a>
                        </router-link>-->
                    </ul>
                </li>
            </ul>
        </div>
        
        <div class="fb-email-btn-bar">
            <button class="btn btn-primary btn-block" data-wenk="" data-wenk-pos="top" @click="fb_email_show=true"><i class="fa fa-comment"></i>  </button>
        </div>
        

        <Modal v-if="fb_email_show" @close="fb_email_show=false">
            <div slot="header">
                <h4 class="modal-title text-center"></h4>
            </div>
            <div slot="body" class="text-center">
                <div class="row">
                    <div class="form-horizontal">
                        <div class="form-group" :class="{'has-error':fb_description_err}">
                            <label class="col-lg-3 control-label text-warning">* </label>

                            <div class="col-lg-8">
                                <textarea v-model="fb_description" class="form-control" placeholder="" rows="5"></textarea>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div slot="footer">
                <button type="button" class="btn btn-primary" @click="sendFbEmail()"> </button>
            </div>
        </Modal>
        
    </div>
</template>
<script>
    import Modal from '../components/Modal.vue'
    import {
        tip,
        ajax
    } from '../tools/tools.js'

    export default {
        name: 'Sidemenu',
        components:{
            Modal
        },
        data() {
            return {
                showLink:'req',//req|rec|''
                active_fb_list:'',
                fb_email_show: false,
                fb_description: '',
                fb_description_err: false
            }
        },
        props: {

        },
        watch:{
            $route(v,ov){
                if(v.name=='requirementpage'||v.name=='bugpage'||v.name=='otherreqpage'||v.name=='apppage'||v.name=='urlpage'||v.name=='wifipage'){
                    this.showLink = 'req';
                    this.active_fb_list = '';
                } else if(v.name=='workingpage'||v.name=='recodepage'||(v.name=='fbdetailpage'&&v.params.routename!='s')){
                    this.showLink = 'rec';
                    if(v.name=='workingpage'||v.params.routename=='w'){
                        this.active_fb_list = 'w';
                    }
                    if(v.name=='recodepage'||v.params.routename=='r'){
                        this.active_fb_list = 'r';
                    }

                } else {
                    this.showLink = '';
                    this.active_fb_list = '';
                }

            },
            fb_email_show(v, ov) {
                if (v == false) {
                    this.fb_description = '';
                    this.fb_description_err = false;
                }
            }
        },
        methods: {
            switchLink(link){
                if(link=='req'){
                    this.$root.$router.push({
                        name:'requirementpage'
                    });
                } else if(link=='rec'){
                    this.$root.$router.push({
                        name:'workingpage'
                    });
                }
            },
            sendFbEmail() {
                var self = this;
                self.fb_description_err = false;

                if($.trim(self.fb_description).length==0){
                    self.fb_description_err = true;
                } else {
                    ajax.sendFeedbackEmail({
                        param:{
                            description:self.fb_description
                        },
                        cb(data){
                            tip.succ('');
                            self.fb_description = '';
                            self.fb_description_err = false;
                            self.fb_email_show = false;
                        }
                    })
                }
            }
        },
        mounted(){
            var v = this.$route;
            if(v.name=='requirementpage'||v.name=='bugpage'||v.name=='otherreqpage'||v.name=='apppage'||v.name=='urlpage'||v.name=='wifipage'){
                this.showLink = 'req';
            } else if(v.name=='workingpage'||v.name=='recodepage'||(v.name=='fbdetailpage'&&v.params.routename!='s')){
                this.showLink = 'rec';
                if(v.name=='workingpage'||v.params.routename=='w'){
                    this.active_fb_list = 'w';
                }
                if(v.name=='recodepage'||v.params.routename=='r'){
                    this.active_fb_list = 'r';
                }
            } else {
                this.showLink = '';
            }
        }
    }
</script>
<style scoped>
    .navbar-side {
        position: fixed;
        box-shadow: 3px 0 6px rgba(0, 0, 0, 0.3);
        top: 50px;
        left: 0px;
        bottom: 0px;
        width: 180px;
        background-color: #5184d2;
        z-index: 2;
        overflow-y: auto;
        overflow-x: hidden;
    }

    .navbar-side .nav-second-level li.active a {
        background-color: #5184d2;
    }

    .fb-email-btn-bar{
        position: absolute;
        z-index: 2;
        bottom: 10px;
        left: 10px;
        right: 10px;
    }
</style>


