<template>
    <div class="middle-box text-center loginscreen animated fadeInDown">
        <div class="load-ele login-box">
            <div>
                <h1 class="logo-name">AVL</h1>
            </div>
            <h3 class="m-b-lg">CO</h3>
            <h6></h6>
            <form class="m-t" @submit.prevent="login">
                <div class="form-group" :class="{'has-error':pwdHasError}">
                    <input type="text" class="form-control" placeholder="" v-model="username">
                </div>
                <div class="form-group" :class="{'has-error':pwdHasError}">
                    <input type="password" class="form-control" placeholder="" v-model="password">
                </div>
                <div class="form-group" :class="{'has-error':vcodeHasError}" v-if="err_num>2">
                    <div class="row">
                        <div class="col-lg-7">
                            <input type="text" class="form-control" placeholder="" v-model="vcode">
                        </div>
                        <div class="col-lg-5" data-wenk="" data-wenk-pos="right">
                            <img height="34" src="https://co.avlsec.com/cfb/Api.php/Customer/getVerifyCode" id="vcodeimg" @click="reloadVcode">
                        </div>
                    </div>
                    
                </div>
                <button class="btn btn-primary block full-width" type="submit"> </button>
                <div class="form-group">
                    <button class="btn btn-link pull-right" @click.prevent="gotoSignUp()"> <i class="fa fa-long-arrow-right"></i></button>
                </div>
            </form>
        </div>
    </div>
</template>
<script>
    import {
        tip,
        ajax
    } from '../tools/tools'
    import {cfg} from '../config/config'

    export default {
        data: function () {
            return {
                username: '',
                unHasError: false,
                password: '',
                pwdHasError: false,
                vcode:'',
                vcodeHasError:false,
                err_num:localStorage.co_login_err_time?parseInt(localStorage.co_login_err_time):0
            }
        },
        watch:{
            err_num(v,ov){
                console.log(v,'vcode')
                if(v>2){
                    this.reloadVcode();
                }
            }
        },
        methods: {
            login: function () {
                var self = this;
                var isPass = true;
                self.unHasError = false;
                self.pwdHasError = false;
                self.vcodeHasError = false;
                if ($.trim(self.username).length == 0) {
                    self.unHasError = true;
                    isPass = false;
                }
                if ($.trim(self.password).length == 0) {
                    self.pwdHasError = true;
                    isPass = false;
                }
                if ($.trim(self.vcode).length == 0&&self.err_num>2) {
                    self.vcodeHasError = true;
                    isPass = false;
                }
                if (isPass) {
                    var par = {
                        username: self.username,
                        password: self.password
                    }

                    if(self.err_num>2){
                        par.verify_code = self.vcode;
                    }

                    ajax.login({
                        param: par,
                        cb:function(data){
                            if(data.errCode==0){
                                localStorage.coc_token = data.data.CO_SSID_OP;
                                localStorage.coc_username = data.data.username;
                                self.$root.USER_NAME = data.data.username;
                                
                                localStorage.co_login_err_time = '0';
                                self.err_num = 0;
                                if(self.$root.$route.query.redirect){
                                    self.$root.$router.replace({
                                        path:self.$root.$route.query.redirect
                                    });
                                } else {
                                    self.$root.$router.replace({
                                        name:'index'
                                    });
                                } 
                            }  
                            
                        },
                        failCb:function(data){
                            if(data.errCode==10004){
                                self.err_num++;
                                localStorage.co_login_err_time = self.err_num;
                            }
                            if(data.errCode==10006||data.errCode==10007){
                                self.err_num = 3;
                                self.reloadVcode();
                            }
                        }
                    });
                } else {
                    tip.err(',,');
                }
            },
            gotoSignUp(){
                this.$router.push({
                    name:'signup'
                })
            },
            reloadVcode(){
                $('#vcodeimg').attr({
                    src:''
                });
                $('#vcodeimg').attr({
                    src:cfg.baseUrl+'/Api.php/Customer/getVerifyCode'
                });
            }

        },
        mounted(){
            console.log(this.err_num);
        }
    }
</script>


