<template>
    <input :name="dtpname" class="form-control select-box-input" v-model="time" :class="[myClass]" type="text" id="messageSendTime" readonly>
</template>

<script>
    import '../vendor/datetimepicker/bootstrap-datetimepicker.js'
    import '../vendor/datetimepicker/bootstrap-datetimepicker.zh-CN.js'

    export default {
        name: 'DateTimePicker',
        data() {
            return {
                time: ''
            }
        },
        props:{
            value:{
                type:String
            },
            myClass:{
                type:String,
                default:''
            },
            dtpname:{
                type:String,
                default:''
            },
            startDate:{
                type:String,
                default:'2000-01-01 10:00'
            },
            endDate:{
                type:String,
                default:'2100-01-01 10:00'
            },
            startView:{
                type:String,
                default:'month'
            },
            minView:{
                type:String,
                default:'hour'
            },
            format:{
                type:String,
                default:'yyyy-mm-dd hh:ii'
            },
            minuteStep:{
                type:Number,
                default:1
            }
        },
        watch:{
            value(v,ov){
                this.time = this.value;
            }
        },
        methods: {
            dateDefind() {
                var self = this;
                
                //
                $('#messageSendTime').datetimepicker({
                    language:'zh-CN',
                    minView: self.minView,
                    format: self.format,
                    todayBtn: 1,
                    autoclose: 1,
                    startView:self.startView,
                    minuteStep:self.minuteStep,
                    startDate:self.startDate,
                    endDate:self.endDate
                });
                //datatime
                $('#messageSendTime').datetimepicker()
                    .on('hide', function (ev) {
                        var value = $("#messageSendTime").val();
                        self.time = value;
                        self.$emit('input',self.time);
                    });
            }
        },
        mounted: function () {
            this.dateDefind();
            $('#messageSendTime').datetimepicker('update');
        }
    }
</script>

<style scoped>

</style>


