<template>
    <div class="viewport">
        <div class="row">
            <div class="col-lg-12">
                <blockquote>
                    <div>
                        <p class="text-primary inline-block-p">
                            
                        </p>
                        <div class="blockquote-form text-right small-text">
                            <span></span>
                        </div>
                        <div class="blockquote-form">
                            <div class="form-group">
                                <select class="form-control bot-border" v-model="status">
                                    <option value=""></option>
                                    <option value="1"></option>
                                    <option value="2"></option>
                                    <option value="3"></option>
                                </select>
                            </div>
                        </div>
                        <div class="blockquote-form pull-right small-text">
                            <span> <b class="text-primary">{{total}}</b> </span>
                            <span>&nbsp;|&nbsp;</span>
                            <span> <b class="text-danger">{{wait_for_accept}}</b> </span>
                            <span>&nbsp;|&nbsp;</span>
                            <span> <b class="text-warning">{{unread_update}}</b> </span>
                        </div>
                    </div>

                </blockquote>
            </div>
        </div>
        <div class="center-table">
            <div class="row">
                <div class="col-lg-12">
                    <div class="recode-list-layout">
                        <template v-for="(objv,objk,obji) in data_obj">
                            <div class="date-row"><div>{{objk | isToday}}</div></div>
                            <template v-for="(arrv,arri) in objv">
                                <div class="recode-row" @click="goToDetail(arrv)" :class="{'status-1':arrv.status=='1','status-2':arrv.status=='2','status-3':arrv.status=='3'}">
                                    <div class="recode-grid grid-1">{{arrv.number}}</div>
                                    <div class="recode-grid grid-2">{{arrv.description}}</div>
                                    <div class="recode-grid grid-3">{{arrv.status |statusStr}}</div>
                                    <div class="recode-grid grid-4">{{arrv.type |typeStr}}</div>
                                    <div class="recode-grid grid-5">
                                        <!--<i class="fa fa-circle text-success" aria-hidden="true"></i>-->
                                        <i class="fa fa-chevron-right" aria-hidden="true"></i>
                                    </div>
                                </div>
                            </template>
                        </template>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>
<script>
    import Modal from '../components/Modal.vue'
    import {testData} from '../config/test.js'
    import {
        tip,
        ajax
    } from '../tools/tools.js'

    export default {
        components: {
            Modal
        },
        data() {
            return {
                total: 0,
                wait_for_accept: 0,
                unread_update: 0,

                status: '',
                data_obj:{}
            }
        },
        watch: {
            status(v, ov) {
                var par = {};
                par.history = '0';
                if (v != '') {
                    par.status = v;
                }
                this.getFbList(par);
            }
        },
        filters:{
            isToday(v){
                var t = new Date();
                var ty = t.getFullYear().toString();
                var tm = t.getMonth()>8?(t.getMonth()+1).toString():('0'+(t.getMonth()+1));
                var td = t.getDate().toString();
                var todayStr = ty+'-'+tm+'-'+td;

                return v==todayStr?'':v;
            },
            statusStr(v){
                var sObj = {
                    '1':'-',
                    '2':'-',
                    '3':'-',
                    '4':'-'
                }

                return sObj[v];
            },
            typeStr(v){
                var tObj = {
                    '1':'',
                    '2':'URL',
                    '3':'WiFi',
                    '4':'',
                    '5':'bug',
                    '6':'',
                }
                return tObj[v];
            }
        },
        methods: {
            getFbList(par) {
                var self = this;
                
                ajax.getFeedbackList({
                    param:par,
                    cb:function(data){
                        self.total = data.data.total;
                        self.wait_for_accept = data.data.wait_for_accept;
                        self.unread_update = data.data.unread_update;
                        
                        var dataList = data.data.data;
                        var dataObj = {};

                        dataList.map((v,i)=>{
                            var dateKey = v.addtime.substring(0,10);
                            if(dataObj.hasOwnProperty(dateKey)){
                                dataObj[dateKey].push(v);
                            } else {
                                dataObj[dateKey] = [v];
                            }
                        });

                        self.data_obj = dataObj;

                    },
                    failCb:function(data){
                        
                    }
                })

            },
            goToDetail(item){
                var self = this;

                self.$root.$router.push({
                    name:'fbdetailpage',
                    params:{
                        number:item.number,
                        routename:'w'
                    }
                }); 
            },
            getDate(dateStr){
                var dayStr = dateStr.substring(0,10);
                var YYYY = parseInt(dayStr.split('-')[0]);
                var MM = parseInt(dayStr.split('-')[1])-1;
                var DD = parseInt(dayStr.split('-')[2]);

                var timeStr = dateStr.substring(11,dateStr.length);
                var hh = parseInt(timeStr.split(':')[0]);
                var mm = parseInt(timeStr.split(':')[1]);
                var ss = parseInt(timeStr.split(':')[2]);

                return new Date(YYYY,MM,DD,hh,mm,ss);
            }
        },
        mounted(){
            this.getFbList({history:'0'});
        }
    }
</script>
<style scoped>
    .blockquote-form .form-group {
        margin-bottom: 0px;
    }

    .form-control.bot-border {
        border: none;
        border-bottom: dotted 1px #e5e6e7;
    }

    .inline-block-p {
        display: inline-block;
        margin-bottom: 0;
        margin-right: 30px;
    }

    .blockquote-form {
        display: inline-block;
        margin-left: 10px;
        vertical-align: middle;
    }

    .small-text span {
        font-size: 70%;
    }
    
    .date-row{
        border-bottom: solid 1px #e7eaec;
        height: 15px;
        margin-bottom: 15px;
        margin-left: 80px;
    }

    .date-row>div{
        position: relative;
        left: -80px;
        top: 5px;
    }
    .recode-row{
        background-color: #efefef;
        padding: 8px 15px;
        margin-bottom: 10px;
        border-radius: 4px;
        transition: box-shadow 0.25s ease-in-out;
        border-width: 1px;
        border-style: solid;
        cursor: pointer;
    }
    .recode-row:hover{
        box-shadow: 0 1px 3px 0 rgba(0,0,0,.2), 0 1px 1px 0 rgba(0,0,0,.14), 0 2px 1px -1px rgba(0,0,0,.12);
    }
    .recode-row.status-1{
        border-color: #607d8b;
    }
    .recode-row.status-2{
        border-color: #03a9f4;
    }
    .recode-row.status-3{
        border-color: #00bcd4;
    }

    .recode-grid{
        display: inline-block;
        vertical-align: middle;
    }

    .recode-grid.grid-1{
        width: 100px;
        font-weight: bold;
    }
    .recode-grid.grid-2{
        width: 200px;
        text-overflow:ellipsis;
        white-space: nowrap;
        overflow: hidden;
        padding: 0 5px;
    }
    .recode-grid.grid-3{
        width: 200px;
    }
    .recode-row.status-1 .recode-grid.grid-3{color:#77a5b0;}
    .recode-row.status-2 .recode-grid.grid-3{color:#03a9f4;}
    .recode-row.status-3 .recode-grid.grid-3{color:#00bcd4;}
    .recode-grid.grid-4{
        width: 200px;
    }
    .recode-grid.grid-5{
        width: 50px;
        text-align: right;
    }

    .recode-grid.grid-5 .fa-chevron-right{
        position: relative;
        left:0px;
        
    }

    .recode-row:hover .recode-grid.grid-5 .fa-chevron-right{
        animation-name:move;
        animation-duration:1.5s;
        animation-timing-function:ease-out;
        animation-iteration-count:infinite;
    }

    @keyframes move{
        0% {left:0px;}
        100% {left:12px;}
    }
</style>


