<template>
    <div class="viewport">
        <div class="row">
            <div class="col-lg-12">
                <blockquote>
                    <p class="text-primary"><a href="###" @click.prevent="$router.push({name:'requirementpage'})"></a> &gt; URL</p>
                </blockquote>
            </div>
        </div>
        <div class="center-form" v-show="!fbCBInfoPanelShow">
            <div class="row">
                <form class="form-horizontal" id="appform" enctype="multipart/form-data">
                    <input type="hidden" name="type" value="2">
                    <div class="form-group" :class="{'has-error':urlDescriptionErr}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> </label>

                        <div class="col-lg-8">
                            <input type="text" name="description" v-model="urlDescription" placeholder="20" class="form-control">
                        </div>
                    </div>
                    <div class="form-title m-t-lg">
                        <div class="form-title-l-txt"></div>
                        <div class="form-title-line"></div>
                        <div class="form-title-r-txt"></div>
                    </div>
                    <div class="form-group" :class="{'has-error':urlObjErr}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> </label>


                        <div class="col-lg-8">
                            <textarea name="" v-model="urlObj" name="detail[misjudgment_object]" class="form-control" placeholder=",,10,10" rows="5"></textarea>
                        </div>
                        
                    </div>
                    <div class="form-group" :class="{'has-error':urlObjErr}">
                        <label class="col-lg-3 control-label"></label>

                        <div class="col-lg-8">
                            <FileInput ipname="file" v-model="file"></FileInput>
                        </div>
                    </div>
                    <div class="form-title m-t-lg">
                        <div class="form-title-l-txt"></div>
                        <div class="form-title-line"></div>
                        <div class="form-title-r-txt"></div>
                    </div>
                    <div class="form-group">
                        <label class="col-sm-3 control-label"></label>

                        <div class="col-sm-8">
                            <label class="radio-inline">
                                <input type="radio" value="1" name="detail[misjudgment_type]" v-model="misjudgmentType"></label>
                            <label class="radio-inline">
                                <input type="radio" value="2" name="detail[misjudgment_type]" v-model="misjudgmentType"></label>
                        </div>
                    </div>
                    <div class="form-group" :class="{'has-error':misjudgmentDatetimeErr}">
                        <label class="col-lg-3 control-label"><span class="text-warning">*</span> </label>

                        <div class="col-lg-8">
                            <DateTimePicker v-model="misjudgmentDatetime" :end-date="int_time" dtpname="detail[misjudgment_datetime]"></DateTimePicker>
                        </div>
                    </div>
                    <div class="form-group">
                        <label class="col-lg-3 control-label"></label>

                        <div class="col-lg-8">
                            <textarea name="" v-model="brief" name="detail[brief]" class="form-control" placeholder="" rows="5"></textarea>
                        </div>
                    </div>
                    <div class="form-group">
                        <div class="col-lg-offset-3 col-lg-3 text-left">
                            <button class="btn btn-primary btn-block" @click.prevent="checkAddFeedback"> </button>
                        </div>
                    </div>
                </form>
            </div>
        </div>
        <Modal v-if="confirmFBPopShow" @close="confirmFBPopShow=false">
            <div slot="header">
                <h4 class="modal-title text-center"></h4>
            </div>
            <div slot="body" class="text-center">
                <p>,,</p>
            </div>
            <div slot="footer">
                <button type="button" class="btn btn-primary" @click="confirmAddFB()" :disabled="adding">
                <i class="fa fa-refresh fa-spin fa-fw" v-show="adding"></i>
                 
                </button>
            </div>
        </Modal>

        <FbCBInfoPanel v-show="fbCBInfoPanelShow" :fbNumber="fbNumber" :fbId="fbId" @goOnFb="goOnFb()"></FbCBInfoPanel>

    </div>
</template>
<script>
import DateTimePicker from '../components/DateTimePicker.vue'
import Modal from '../components/Modal.vue'
import FileInput from '../components/FileInput.vue'
import FbCBInfoPanel from '../components/FbCBInfoPanel.vue'
import {
        tip,
        ajax
    } from '../tools/tools.js'

export default {
    components:{DateTimePicker,Modal,FileInput,FbCBInfoPanel},
    data(){
        return {
            fd:null,
            urlDescription:'',
            urlDescriptionErr:false,
            urlObj:'',
            file:null,
            urlObjErr:false,
            misjudgmentType:'',
            int_time:(new Date().getFullYear())+'-'+(new Date().getMonth()+1)+'-'+(new Date().getDate()),
            misjudgmentDatetime:'',
            misjudgmentDatetimeErr:false,
            brief:'',

            confirmFBPopShow:false,
            fbId:'',
            fbNumber:'',
            fbCBInfoPanelShow:false,

            adding:false
        }
    },
    watch:{

    },
    methods:{
        checkAddFeedback(){
            var self = this;
            var isPass = true;
            if ($.trim(self.urlDescription).length == 0||$.trim(self.urlDescription).length > 20) {
                self.urlDescriptionErr = true;
                isPass = false;
            } else {
                self.urlDescriptionErr = false;
            }
            
            var filelen = typeof(self.file)=='string'?self.file.length:1;

            var regRN = /\s+/;
            var url_obj_list = self.urlObj.length>0?self.urlObj.split(regRN):[];
            if ((url_obj_list.length==0||url_obj_list.length>10)&&(self.file==null||filelen==0)) {
                self.urlObjErr = true;
                isPass = false;
            } else {
                self.urlObjErr = false;
            }
            if ($.trim(self.misjudgmentDatetime).length == 0) {
                self.misjudgmentDatetimeErr = true;
                isPass = false;
            } else {
                self.misjudgmentDatetimeErr = false;
            }

            
            if(isPass){
                self.fbNumber = '';
                self.confirmFBPopShow = true;

            } else {
                tip.err('');
            }

            

        },
        confirmAddFB(){
            var self = this;
            self.fd = new FormData(document.getElementById('appform'));
            self.adding = true;
            ajax.addFeedback({
                param: self.fd,
                cb:function(data){
                    self.fbNumber = data.data.number;
                    self.confirmFBPopShow = false;
                    self.fbCBInfoPanelShow = true;
                    self.adding = false;
                },
                failCb:function(data){
                    self.adding = false;
                }
            });

            // self.fbNumber = '1';
            // self.confirmFBPopShow = false;
            // self.fbCBInfoPanelShow = true;
        },
        goOnFb(){
            var self = this;
            self.urlDescription = ''; 
            self.urlDescriptionErr = false;
            self.rlUrl = '';
            self.urlObjErr = false;
            self.file = null;
            self.misjudgmentType = '';
            self.misjudgmentDatetime = '';
            self.misjudgmentDatetimeErr = false;
            self.brief = '';

            self.fbNumber = '';
            self.fbCBInfoPanelShow = false;
        }
    }
}
</script>
<style scoped>

</style>



